<template>
    <div>
        <!-- 这是一个全局组件 -->
        <TypeNav />
        <!-- 使用局部组局 -->
        <ListContainer />
        <TodayRecommend />
        <Rank />
        <Like />
        <Floor v-for="(floor, index) in floorList" :key="floor.id" :floorList="floorList[index]" />
        <Brand />
    </div>
</template>

<script>
// 引入home页的局部组件
import ListContainer from "./ListContainer";
import TodayRecommend from "./TodayRecommend";
import Rank from "./Rank";
import Like from "./Like";
import Floor from "./Floor";
import Brand from "./Brand";

export default {
    name: "Home",
    components: {
        ListContainer,
        TodayRecommend,
        Rank,
        Like,
        Floor,
        Brand,
    },
    mounted() {
        // 组件挂载，获取子组件getFloorList数据进行展示
        this.$store.dispatch("home/getFloorList");
        // home组件挂载，如果用户有token，派发，获取用户登录信息的action，成功后在网页左上角(Header组件中）进行展示
        // this.$store.dispatch("user/getUserInfoByToken")
    },
    computed: {
        floorList() {
            return this.$store.state.home.floorList;
        },
    },
};
</script>

