<template>
    <div class="trade-container">
        <h3 class="title">填写并核对订单信息</h3>
        <div class="content">
            <h5 class="receive">收件人信息</h5>
            <!-- 用户地址信息 -->
            <div
                class="address clearFix"
                v-for="userAddress in userAddressList"
                :key="userAddress.id"
            >
                <!-- consignee:收件人的意思  当服务器返回的isDefault为1时表示默认勾选（默认地址） -->
                <span class="username" :class="{ selected: userAddress.isDefault == 1 }">{{
                    userAddress.consignee
                }}</span>
                <p @click="changeDefault(userAddress)">
                    <span class="s1">{{ userAddress.fullAddress }}</span>
                    <span class="s2">{{ userAddress.phoneNum }}</span>
                    <!-- isDefault为1时才显示 -->
                    <span class="s3" v-show="userAddress.isDefault == 1">默认地址</span>
                </p>
            </div>

            <div class="line"></div>
            <h5 class="pay">支付方式</h5>
            <div class="address clearFix">
                <span class="username selected">在线支付</span>
                <span class="username" style="margin-left: 5px">货到付款</span>
            </div>
            <div class="line"></div>
            <h5 class="pay">送货清单</h5>
            <div class="way">
                <h5>配送方式</h5>
                <div class="info clearFix">
                    <span class="s1">天天快递</span>
                    <p>配送时间：预计8月10日（周三）09:00-15:00送达</p>
                </div>
            </div>
            <div class="detail">
                <h5>商品清单</h5>
                <ul
                    class="list clearFix"
                    v-for="detailOrder in detailArrayList"
                    :key="detailOrder.skuId"
                >
                    <li>
                        <img :src="detailOrder.imgUrl" style="width: 82px; height: 82px" />
                    </li>
                    <li>
                        <p>
                            {{ detailOrder.skuName }}
                        </p>
                        <h4>7天无理由退货</h4>
                    </li>
                    <li>
                        <h3>￥{{ detailOrder.orderPrice }}.00</h3>
                    </li>
                    <li>X{{ detailOrder.skuNum }}</li>
                    <li>有货</li>
                </ul>
            </div>
            <div class="bbs">
                <h5>买家留言：</h5>
                <textarea
                    placeholder="建议留言前先与商家沟通确认"
                    class="remarks-cont"
                    v-model="userMsg"
                ></textarea>
            </div>
            <div class="line"></div>
            <div class="bill">
                <h5>发票信息：</h5>
                <div>普通发票（电子） 个人 明细</div>
                <h5>使用优惠/抵用</h5>
            </div>
        </div>
        <div class="money clearFix">
            <ul>
                <li>
                    <b
                        ><i>{{ orderInfo.totalNum }}</i
                        >件商品，总商品金额</b
                    >
                    <span>¥{{ orderInfo.totalAmount }}.00</span>
                </li>
                <li>
                    <b>返现：</b>
                    <span>0.00</span>
                </li>
                <li>
                    <b>运费：</b>
                    <span>0.00</span>
                </li>
            </ul>
        </div>
        <div class="trade">
            <div class="price">
                应付金额:<span>¥{{ orderInfo.totalAmount }}.00</span>
            </div>
            <div class="receiveInfo">
                寄送至:
                <span>{{ defaultrAddress.fullAddress }}</span>
                收货人：<span>{{ defaultrAddress.consignee }}</span>
                <span>{{ defaultrAddress.phoneNum }}</span>
            </div>
        </div>
        <div class="sub clearFix">
            <a class="subBtn" @click="getSubmitOrderInfo">提交订单</a>
        </div>
    </div>
</template>

<script>
import { mapState } from "vuex";
export default {
    name: "Trade",
    data() {
        return {
            // 买家留言
            userMsg: "",
        };
    },
    computed: {
        // 用户地址信息 userAddressList | 订单交易页信息 orderInfo
        ...mapState("trade", ["userAddressList", "orderInfo"]),
        // 被选中的用户地址信息(默认地址)
        defaultrAddress() {
            // 找出用户地址信息表中，isDefault为1 的元素返回
            return (
                this.userAddressList.find((item) => {
                    return item.isDefault == "1";
                    /* 当服务器返回的isDefault都是0时，find方法找不到 isDefault为1 的元素所以返回undefined ，
                     为防止报错提供一个空对象 */
                }) || {}
            );
        },
        // 商品订单详情列表
        detailArrayList() {
            return this.orderInfo.detailArrayList;
        },
    },
    methods: {
        // 更改默认地址（改变 isDefault 的值）
        changeDefault(userAddress) {
            this.userAddressList.forEach((element) => {
                // 将地址信息中所有isDefault的值都变为0
                element.isDefault = "0";
            });
            // 将我们点击的 isDefault 的值变为1
            userAddress.isDefault = "1";
        },
        // 提交订单，准备支付的回调
        async getSubmitOrderInfo() {
            // 准备要携带的参数
            let tradeNo = this.orderInfo.tradeNo;
            let data = {
                consignee: this.defaultrAddress.consignee, //收件人姓名
                consigneeTel: this.defaultrAddress.phoneNum, //收件人电话
                deliveryAddress: this.defaultrAddress.fullAddress, //收件地址
                paymentWay: "ONLINE", //支付方式(本项目只能微信在线支付)
                orderComment: this.userMsg, //订单备注
                orderDetailList: this.detailArrayList, //存储多个商品对象的数组
            };
            let results = await this.$API.reqSubmitOrder(tradeNo, data);
            // console.log(results);
            if (results.code == 200) {
                // 表示向服务器提交支付订单信息成功
                // 存储服务器返回的订单号
                let orderId = results.data;
                // 跳转到支付页面
                this.$router.push({
                    name: "pay",
                    // 携带query参数 服务器返回的订单号 orderId
                    query: {
                        orderId: orderId,
                    },
                });
            } else {
                // 提醒用户错误信息
                alert(results.message);
            }
        },
    },
    mounted() {
        // 页面挂载，派发action，向后台请求数据
        // 用户地址信息
        this.$store.dispatch("trade/getUserAddressList");
        // 订单交易信息
        this.$store.dispatch("trade/getOrderTradeInfo");
    },
};
</script>

<style lang="less" scoped>
.trade-container {
    .title {
        width: 1200px;
        margin: 0 auto;
        font-size: 14px;
        line-height: 21px;
    }

    .content {
        width: 1200px;
        margin: 10px auto 0;
        border: 1px solid rgb(221, 221, 221);
        padding: 25px;
        box-sizing: border-box;

        .receive,
        .pay {
            line-height: 36px;
            margin: 18px 0;
        }

        .address {
            padding-left: 20px;
            margin-bottom: 15px;

            .username {
                float: left;
                width: 100px;
                height: 30px;
                line-height: 30px;
                text-align: center;
                border: 1px solid #ddd;
                position: relative;
            }

            .username::after {
                content: "";
                display: none;
                width: 13px;
                height: 13px;
                position: absolute;
                right: 0;
                bottom: 0;
                background: url(./images/choosed.png) no-repeat;
            }

            .username.selected {
                border-color: #e1251b;
            }

            .username.selected::after {
                display: block;
            }

            p {
                width: 610px;
                float: left;
                line-height: 30px;
                margin-left: 10px;
                padding-left: 5px;
                cursor: pointer;

                .s1 {
                    float: left;
                }

                .s2 {
                    float: left;
                    margin: 0 5px;
                }

                .s3 {
                    float: left;
                    width: 56px;
                    height: 24px;
                    line-height: 24px;
                    margin-left: 10px;
                    background-color: #878787;
                    color: #fff;
                    margin-top: 3px;
                    text-align: center;
                }
            }

            p:hover {
                background-color: #ddd;
            }
        }

        .line {
            height: 1px;
            background-color: #ddd;
        }

        .way {
            width: 1080px;
            height: 110px;
            background: #f4f4f4;
            padding: 15px;
            margin: 0 auto;

            h5 {
                line-height: 50px;
            }

            .info {
                margin-top: 20px;

                .s1 {
                    float: left;
                    border: 1px solid #ddd;
                    width: 120px;
                    height: 30px;
                    line-height: 30px;
                    text-align: center;
                    margin-right: 10px;
                }

                p {
                    line-height: 30px;
                }
            }
        }

        .detail {
            width: 1080px;

            background: #feedef;
            padding: 15px;
            margin: 2px auto 0;

            h5 {
                line-height: 50px;
            }

            .list {
                display: flex;
                justify-content: space-between;

                li {
                    line-height: 30px;

                    p {
                        margin-bottom: 20px;
                    }

                    h4 {
                        color: #c81623;
                        font-weight: 400;
                    }

                    h3 {
                        color: #e12228;
                    }
                }
            }
        }

        .bbs {
            margin-bottom: 15px;

            h5 {
                line-height: 50px;
            }

            textarea {
                width: 100%;
                border-color: #e4e2e2;
                line-height: 1.8;
                outline: none;
                resize: none;
            }
        }

        .bill {
            h5 {
                line-height: 50px;
            }

            div {
                padding-left: 15px;
            }
        }
    }

    .money {
        width: 1200px;
        margin: 20px auto;

        ul {
            width: 220px;
            float: right;

            li {
                line-height: 30px;
                display: flex;
                justify-content: space-between;

                i {
                    color: red;
                }
            }
        }
    }

    .trade {
        box-sizing: border-box;
        width: 1200px;
        padding: 10px;
        margin: 10px auto;
        text-align: right;
        background-color: #f4f4f4;
        border: 1px solid #ddd;

        div {
            line-height: 30px;
        }

        .price span {
            color: #e12228;
            font-weight: 700;
            font-size: 14px;
        }

        .receiveInfo {
            color: #999;
        }
    }

    .sub {
        width: 1200px;
        margin: 0 auto 10px;

        .subBtn {
            float: right;
            width: 164px;
            height: 56px;
            font: 700 18px "微软雅黑";
            line-height: 56px;
            text-align: center;
            color: #fff;
            background-color: #e1251b;
        }
    }
}
</style>
