<template>
    <!--楼层-->
    <div class="floor">
        <div class="py-container">
            <div class="title clearfix">
                <h3 class="fl">{{ floorList.name }}</h3>
                <div class="fr">
                    <ul class="nav-tabs clearfix">
                        <li class="active">
                            <a href="#tab1" data-toggle="tab">{{ floorList.navList[0].text }}</a>
                        </li>
                        <li v-for="(nav, index) in newFloorList" :key="index">
                            <a href="#tab1" data-toggle="tab">{{ nav.text }}</a>
                        </li>
                    </ul>
                </div>
            </div>
            <div class="tab-content">
                <div class="tab-pane">
                    <div class="floor-1">
                        <div class="blockgary">
                            <ul class="jd-list">
                                <li v-for="(keyword, index) in floorList.keywords" :key="index">
                                    {{ keyword }}
                                </li>
                            </ul>
                            <img :src="floorList.imgUrl" />
                        </div>
                        <div class="floorBanner">
                            <!-- swiper 轮播图 -->
                            <!-- 使用全局组件Carousel（轮播图） -->
                            <Carousel :list="floorList.carouselList"/>
                        </div>
                        <div class="split">
                            <span class="floor-x-line"></span>
                            <div class="floor-conver-pit">
                                <img :src="floorList.recommendList[0]" />
                            </div>
                            <div class="floor-conver-pit">
                                <img :src="floorList.recommendList[1]" />
                            </div>
                        </div>
                        <div class="split center">
                            <img :src="floorList.bigImg" />
                        </div>
                        <div class="split">
                            <span class="floor-x-line"></span>
                            <div class="floor-conver-pit">
                                <img :src="floorList.recommendList[2]" />
                            </div>
                            <div class="floor-conver-pit">
                                <img :src="floorList.recommendList[3]" />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>

export default {
    name: "Floor",
    props: ["floorList"],
    computed: {
        // 过滤掉 "热门和热门2" 返回一个新数组，对原数组无影响
        newFloorList() {
            return this.floorList.navList.filter((nav) => {
                return nav.text !== "热门2" && nav.text !== "热门";
            });
        },
    },
    mounted() {
        // /* 相比较与前面ListContainer组件中的轮播图，这里的数据来自父组件home，通过props传递给floor组件，
        // 数据肯定已经存在了，不在需要异步获取,所以不需要再调用watch和nextTick */
        // var mySwiper = new Swiper(".swiper-container", {
        //     // direction: "vertical", // 垂直切换选项
        //     loop: true, // 循环模式选项
        //     autoplay: {
        //         /* 用户操作swiper之后，是否禁止autoplay。默认为true：停止。
        //         如果设置为false，用户操作swiper之后自动切换不会停止，每次都会重新启动autoplay。
        //         操作包括触碰(touch)，拖动，点击pagination等。 */
        //         disableOnInteraction: false,
        //     },
        //     // 如果需要分页器
        //     pagination: {
        //         el: ".swiper-pagination",
        //         clickable: true, //此参数设置为true时，点击分页器的指示点分页器会控制Swiper切换。
        //     },
        //     // 如果需要前进后退按钮
        //     navigation: {
        //         nextEl: ".swiper-button-next",
        //         prevEl: ".swiper-button-prev",
        //     },
        //     // 如果需要滚动条
        //     // scrollbar: {
        //     //     el: ".swiper-scrollbar",
        //     // },
        // });
    },
};
</script>

<style lang="less" scoped>
.floor {
    margin-top: 15px;

    .py-container {
        width: 1200px;
        margin: 0 auto;

        .title {
            .fl {
                float: left;
                color: #c81623;
                font-size: 20px;
                line-height: 30px;
                margin: 9px 0;
                font-weight: 700;
            }

            .fr {
                float: right;

                .nav-tabs {
                    margin: 10px 0 0;
                    display: inline-block;

                    li {
                        float: left;
                        line-height: 18px;

                        a {
                            padding-top: 1px;
                            font-weight: 400;
                            background-color: #fff;

                            &::after {
                                content: "|";
                                padding: 0 10px;
                            }
                        }

                        &:nth-child(7) {
                            a {
                                &::after {
                                    content: "";
                                }
                            }
                        }

                        &.active {
                            a {
                                color: #e1251b;
                            }
                        }
                    }
                }
            }
        }

        .tab-content {
            border-top: 2px solid #c81623;
            border-bottom: 1px solid #e4e4e4;

            .tab-pane {
                .floor-1 {
                    height: 360px;
                    display: flex;

                    .blockgary {
                        width: 210px;
                        height: 100%;
                        background: #f7f7f7;

                        .jd-list {
                            padding: 15px 0;
                            overflow: hidden;

                            li {
                                list-style-type: none;
                                float: left;
                                width: 40%;
                                margin: 0 10px;
                                border-bottom: 1px solid #e4e4e4;
                                text-align: center;
                                line-height: 26px;
                            }
                        }

                        img {
                            width: 100%;
                        }
                    }

                    .floorBanner {
                        width: 330px;
                        height: 100%;
                    }

                    .split {
                        width: 220px;
                        height: 100%;
                        position: relative;

                        .floor-x-line {
                            position: absolute;
                            background: #e4e4e4;
                            width: 220px;
                            height: 1px;
                            top: 180px;
                        }

                        .floor-conver-pit {
                            width: 100%;
                            height: 50%;

                            img {
                                width: 100%;
                                height: 100%;
                                transition: all 400ms;

                                &:hover {
                                    opacity: 0.8;
                                }
                            }
                        }
                    }

                    .center {
                        border: 1px solid #e4e4e4;
                    }
                }
            }
        }
    }
}
</style>
