<template>
    <div class="paysuccess">
        <div class="success">
            <h3>
                <img src="./images/right.png" width="48" height="48" />
                恭喜您，支付成功啦！
            </h3>
            <div class="paydetail">
                <p class="button">
                    <router-link class="btn-look" to="/center">查看订单</router-link>
                    <router-link class="btn-goshop" to="/">继续购物</router-link>
                </p>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: "PaySuccess",
    // 组件内路由守卫
    beforeRouteEnter(to, from, next) {
        // 在渲染该组件的对应路由被验证前调用
        // 不能获取组件实例 `this` ！
        // 因为当守卫执行时，组件实例还没被创建！
        if (from.path == "/pay") {
          // 从支付页面来的才放行
          next()
        }else{
          // 支付页面外来的，返回原来的路由
          next(false)
        }
    },
};
</script>

<style lang="less" scoped>
.paysuccess {
    margin: 20px auto;
    padding: 25px;
    border: 1px solid rgb(211, 211, 211);
    width: 1200px;
    width: 1148px;

    .success {
        width: 500px;
        margin: 0 auto;

        h3 {
            margin: 20px 0;
            font-weight: 700;
            font-size: 20px;
            line-height: 30px;

            img {
                max-width: 100%;
                vertical-align: middle;
                border: 0;
                margin-right: 14px;
            }
        }

        .paydetail {
            margin-left: 66px;
            font-size: 15px;

            .button {
                margin: 30px 0;
                line-height: 26px;

                a {
                    display: inline-block;
                    box-sizing: border-box;
                    text-align: center;
                    vertical-align: middle;
                    cursor: pointer;
                    border-radius: 2px;
                    user-select: none;
                    font-size: 18px;
                    padding: 4px 20px;
                    line-height: 22px;
                    text-decoration: none;

                    &.btn-look {
                        margin-right: 13px;
                        color: #fff;
                        background-color: #e1251b;
                        border: 1px solid #e1251b;
                    }

                    &.btn-goshop {
                        color: #666;
                        background-color: #eee;
                        border: 1px solid #e1e1e1;
                    }
                }
            }
        }
    }
}
</style>
