<template>
    <div class="cart">
        <h4>全部商品</h4>
        <div class="cart-main">
            <div class="cart-th">
                <div class="cart-th1">全部</div>
                <div class="cart-th2">商品</div>
                <div class="cart-th3">单价（元）</div>
                <div class="cart-th4">数量</div>
                <div class="cart-th5">小计（元）</div>
                <div class="cart-th6">操作</div>
            </div>
            <div class="cart-body">
                <!-- shopInfo:遍历出来的每个商品的具体信息，包括商品id,图片url等 -->
                <ul class="cart-list" v-for="shopInfo in cartInfoList" :key="shopInfo.id">
                    <li class="cart-list-con1">
                        <!-- 通过服务器返回数据中的isChecked属性是否为1，确定是否被选中 -->
                        <input
                            type="checkbox"
                            name="chk_list"
                            :checked="shopInfo.isChecked"
                            @change="changeShopcartChecked($event, shopInfo)"
                        />
                    </li>
                    <li class="cart-list-con2">
                        <img :src="shopInfo.imgUrl" />
                        <div class="item-msg">
                            {{ shopInfo.skuName }}
                        </div>
                    </li>
                    <li class="cart-list-con4">
                        <span class="price">{{ shopInfo.skuPrice }}.00</span>
                    </li>
                    <li class="cart-list-con5">
                        <!-- 通过不同的传参分别不同的元素事件 -->
                        <a
                            href="javascript:void(0)"
                            class="mins"
                            @click="handler('sub', -1, shopInfo)"
                            >-</a
                        >
                        <input
                            autocomplete="off"
                            type="text"
                            :value="shopInfo.skuNum"
                            minnum="1"
                            class="itxt"
                            @change="handler('change', $event.target.value * 1, shopInfo)"
                        />
                        <a
                            href="javascript:void(0)"
                            class="plus"
                            @click="handler('add', 1, shopInfo)"
                            >+</a
                        >
                    </li>
                    <li class="cart-list-con6">
                        <span class="sum">{{ shopInfo.skuNum * shopInfo.skuPrice }}.00</span>
                    </li>
                    <li class="cart-list-con7">
                        <a class="sindelet" @click="DeleteShopcartById(shopInfo)">删除</a>
                        <br />
                        <a href="#none">移到收藏</a>
                    </li>
                </ul>
            </div>
        </div>
        <div class="cart-tool">
            <div class="select-all">
                <input
                    class="chooseAll"
                    type="checkbox"
                    :checked="isAllChecked"
                    @change="checkedAll"
                />
                <span>全选</span>
            </div>
            <div class="option">
                <a @click="deleteAllShopCartChecked">删除选中的商品</a>
                <a href="#none">移到我的关注</a>
                <a href="#none">清除下柜商品</a>
            </div>
            <div class="money-box">
                <div class="chosed">已选择 <span>0</span>件商品</div>
                <div class="sumprice">
                    <em>总价（不含运费） ：</em>
                    <i class="summoney">{{ totalPrices }}</i>
                </div>
                <div class="sumbtn">
                    <router-link class="sum-btn" to="/trade">结算</router-link>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { mapGetters } from "vuex";
// 按需引入lodash函数库中的节流函数（防止用户操作过快，让购买商品出现负数）
import throttle from "lodash/throttle";
export default {
    name: "ShopCart",
    // 当页面挂载，向服务器请求购物车页面的列表数据
    mounted() {
        this.getData();
    },
    methods: {
        // 封装一个请求购物车页面的列表数据的函数
        getData() {
            this.$store.dispatch("shopcart/getShopcartList");
        },
        // 改变购买商品的数量
        // type:用于分辨我们通过哪个元素改变商品购买数量
        /* disNum: -1，表示点击减号购买数减一  1，表示点击加号购买数加一   input是用户输入改变后的值 (不是改变的量)
        通过计算可以得到请求需要的参数skuNum */
        // shopInfo:商品的信息，里面包含商品的id即请求需要的参数skuId
        handler: throttle(async function (type, disNum, shopInfo) {
            // 通过 switch 判断type 不要忘了break
            switch (type) {
                // 如果type为"add",让disNum为1即可
                case "add":
                    disNum = 1;
                    break;
                case "sub":
                    // 如果现在购买数量大于1才能进行减少商品数量的操作
                    // if (shopInfo.skuNum > 1) {
                    //     disNum = -1;
                    // } else {
                    //     // 当出现现在购买商品数量小于等于1时，虽然发请求，但是disNum为0(原封不动)
                    //     disNum = 0;
                    // }
                    // 简化三元表达式:
                    disNum = shopInfo.skuNum > 1 ? -1 : 0;
                    break;
                case "change":
                    //用户输入非法字|用户输入小于1时，虽然发请求，但是disNum为0(原封不动)
                    if (isNaN(disNum) || disNum < 1) {
                        disNum = 0;
                    } else {
                        // 用户输入合法，为防止小数，进行向下取整，并减去原来购买商品的数量，正为加，负为减
                        disNum = parseInt(disNum) - shopInfo.skuNum;
                    }
                    // 简化三元表达式：不建议
                    // disNum = (isNaN(disNum) || disNum < 1) ? 0 : (parseInt(disNum) - shopInfo.skuNum)
                    break;
            }
            // 整理完数据，派发action获取数据,可能成功可能失败
            /* this.$store.dispatch("detail/getAddorUpdateShopcartInfo",{ skuId:shopInfo.skuId, skuNum:disNum })
             // 可以用then和catch方法
            // 成功
            .then(()=>{
                // 重新获取购物车商品数据
                this.getData()
            })
            // 失败
            .catch((error)=>{
                alert(error.message)
            }) */
            // 这里使用try和catch搭配async和
            try {
                // 成功
                await this.$store.dispatch("detail/getAddorUpdateShopcartInfo", {
                    skuId: shopInfo.skuId,
                    skuNum: disNum,
                });
                // 再次请求服务器新数据,进行展示
                this.getData();
            } catch (error) {
                alert(error.message);
            }
        }, 500),
        // 删除购物车的某一项商品
        DeleteShopcartById(shopInfo) {
            this.$store
                .dispatch("shopcart/DeleteShopcartById", shopInfo.skuId)
                .then(() => {
                    // 请求服务器成功,重新发请求获取新数据，进行展示
                    this.getData();
                })
                .catch((error) => {
                    // 请求服务器失败，提醒用户
                    alert(error.message);
                });
        },
        // 购物车页面，切换商品是否被选中
        changeShopcartChecked($event, shopInfo) {
            let checked = $event.target.checked;
            // console.log(shopInfo.skuId,checked);
            // checked为真即被选中，将1赋值给需要携带的参数isChecked，反之checked为假即未被选中，将0赋值给需要携带的参数isChecked
            let isChecked = checked ? 1 : 0;
            // console.log(checked);
            this.$store
                .dispatch("shopcart/changeShopcartChecked", { skuId: shopInfo.skuId, isChecked })
                .then(() => {
                    // 成功请求新数据，切换选中后，再次发请求获取购物车页面展示数据，并重新展示
                    this.getData();
                    // console.log("我执行了");
                })
                .catch((error) => {
                    alert(error.message);
                });
        },
        // 全选勾选框改变，让所有的商品都被选中或者都不被选中 即 isChecked = 1 或 isChecked = 0
        checkedAll($event) {
            if ($event.target.checked) {
                // 全选被选中
                this.cartInfoList.forEach((element) => {
                    this.changeShopcartChecked($event, element);
                });
                //  console.log("全选被选中");
            } else {
                // 全选不选中
                this.cartInfoList.forEach((element) => {
                    this.changeShopcartChecked($event, element);
                });
                // console.log("全选不选中");
            }
        },
        // 删除全部被选中的商品
        deleteAllShopCartChecked() {
            this.$store
                .dispatch("shopcart/deleteAllShopCartChecked", this.cartInfoList)
                // 等待返回的promise是成功
                .then(() => {
                    // 成功，重新获取服务器数据，展示
                    this.getData();
                })
                // 等待返回的promise是失败
                .catch((error) => {
                    // 失败，提醒用户
                    alert(error.message);
                });
        },
    },
    computed: {
        // 映射cartInfoObj
        ...mapGetters("shopcart", ["cartInfoObj"]),
        // cartInfoList才是我们需要的展示购物车数据的数据数组,他至少是一个空数组
        cartInfoList() {
            return this.cartInfoObj.cartInfoList || [];
        },
        // 技术购买商品的总价
        totalPrices() {
            let sum = 0;
            this.cartInfoList.forEach((element) => {
                sum += element.skuNum * element.skuPrice;
            });
            return sum;
        },
        // 是否全选
        isAllChecked() {
            // 注意：如果不简写别忘了return
            if (this.cartInfoList.length > 0) {
                return this.cartInfoList.every((item) => item.isChecked == 1);
            }else{
                return false;
            }
        },
    },
};
</script>

<style lang="less" scoped>
.cart {
    width: 1200px;
    margin: 0 auto;

    h4 {
        margin: 9px 0;
        font-size: 14px;
        line-height: 21px;
    }

    .cart-main {
        .cart-th {
            background: #f5f5f5;
            border: 1px solid #ddd;
            padding: 10px;
            overflow: hidden;

            & > div {
                float: left;
            }

            .cart-th1 {
                width: 25%;

                input {
                    vertical-align: middle;
                }

                span {
                    vertical-align: middle;
                }
            }

            .cart-th2 {
                width: 25%;
            }

            .cart-th3,
            .cart-th4,
            .cart-th5,
            .cart-th6 {
                width: 12.5%;
            }
        }

        .cart-body {
            margin: 15px 0;
            border: 1px solid #ddd;

            .cart-list {
                padding: 10px;
                border-bottom: 1px solid #ddd;
                overflow: hidden;

                & > li {
                    float: left;
                }

                .cart-list-con1 {
                    width: 15%;
                }

                .cart-list-con2 {
                    width: 35%;

                    img {
                        width: 82px;
                        height: 82px;
                        float: left;
                    }

                    .item-msg {
                        float: left;
                        width: 150px;
                        margin: 0 10px;
                        line-height: 18px;
                    }
                }

                // .cart-list-con3 {
                //   width: 20.8333%;

                //   .item-txt {
                //     text-align: center;
                //   }
                // }

                .cart-list-con4 {
                    width: 10%;
                }

                .cart-list-con5 {
                    width: 17%;

                    .mins {
                        border: 1px solid #ddd;
                        border-right: 0;
                        float: left;
                        color: #666;
                        width: 6px;
                        text-align: center;
                        padding: 8px;
                    }

                    input {
                        border: 1px solid #ddd;
                        width: 40px;
                        height: 33px;
                        float: left;
                        text-align: center;
                        font-size: 14px;
                    }

                    .plus {
                        border: 1px solid #ddd;
                        border-left: 0;
                        float: left;
                        color: #666;
                        width: 6px;
                        text-align: center;
                        padding: 8px;
                    }
                }

                .cart-list-con6 {
                    width: 10%;

                    .sum {
                        font-size: 16px;
                    }
                }

                .cart-list-con7 {
                    width: 13%;

                    a {
                        color: #666;
                    }
                }
            }
        }
    }

    .cart-tool {
        overflow: hidden;
        border: 1px solid #ddd;

        .select-all {
            padding: 10px;
            overflow: hidden;
            float: left;

            span {
                vertical-align: middle;
            }

            input {
                vertical-align: middle;
            }
        }

        .option {
            padding: 10px;
            overflow: hidden;
            float: left;

            a {
                float: left;
                padding: 0 10px;
                color: #666;
            }
        }

        .money-box {
            float: right;

            .chosed {
                line-height: 26px;
                float: left;
                padding: 0 10px;
            }

            .sumprice {
                width: 200px;
                line-height: 22px;
                float: left;
                padding: 0 10px;

                .summoney {
                    color: #c81623;
                    font-size: 16px;
                }
            }

            .sumbtn {
                float: right;

                a {
                    display: block;
                    position: relative;
                    width: 96px;
                    height: 52px;
                    line-height: 52px;
                    color: #fff;
                    text-align: center;
                    font-size: 18px;
                    font-family: "Microsoft YaHei";
                    background: #e1251b;
                    overflow: hidden;
                }
            }
        }
    }
}
</style>
