<template>
    <!-- 移动鼠标展示大图 -->
    <div class="spec-preview" @mousemove="showBigImg">
        <!-- 商品展示图 -->
        <!-- <img :src="skuImageObj.imgUrl" /> -->
        <img v-lazy="skuImageObj.imgUrl" />
        <div class="event"></div>

        <!-- 放大图 -->
        <div class="big">
            <img :src="skuImageObj.imgUrl" ref="bigImg" />
        </div>

        <!-- 遮罩层（绿色） -->
        <div class="mask" ref="mask"></div>
    </div>
</template>

<script>
export default {
    name: "Zoom",
    props: ["skuImageList"],
    data() {
        return {
            currentIndex: 0,
        };
    },
    computed: {
        skuImageObj() {
            // 在服务器数据回来前，为防止假报错，设置空对象
            return this.skuImageList[this.currentIndex] || {};
        },
    },
    methods: {
        showBigImg(event) {
            let mask = this.$refs.mask;
            let bigImg = this.$refs.bigImg;
            // 遮罩层初始在左上
            // 鼠标水平移动的像素，减去遮罩层宽度的一半，就是遮罩层向右移动的像素
            // 鼠标垂直移动的像素，减去遮罩层高度的一半，就是遮罩层向下移动的像素
            let left = event.offsetX - mask.offsetWidth / 2;
            let top = event.offsetY - mask.offsetHeight / 2;
            // 约束遮罩层的范围
            if (left <= 0) {
                left = 0;
            }
            if (left >= mask.offsetWidth) {
                left = mask.offsetWidth;
            }
            if (top <= 0) {
                top = 0;
            }
            if (top >= mask.offsetHeight) {
                top = mask.offsetHeight;
            }
            // 改变遮罩层的left和top，以此改变遮罩层的位置,别忘了加 "px"
            (mask.style.left = left + "px"), (mask.style.top = top + "px");
            bigImg.style.left = -2 * left + "px";
            bigImg.style.top = -2 * top + "px";
        },
    },
    mounted() {
        // 绑定 获取当前点击轮播图小图片对应的索引值 事件
        this.$bus.$on("getIndex", (index) => {
            // 更改响应式数据currentIndex
            this.currentIndex = index;
        });
    },
    beforeDestroy() {
        // 在组件销毁前，解绑事件
        this.$bus.$off("getIndex");
    },
};
</script>

<style lang="less">
.spec-preview {
    position: relative;
    width: 400px;
    height: 400px;
    border: 1px solid #ccc;

    img {
        width: 100%;
        height: 100%;
    }

    .event {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        z-index: 998;
    }

    .mask {
        width: 50%;
        height: 50%;
        background-color: rgba(0, 255, 0, 0.3);
        position: absolute;
        left: 0;
        top: 0;
        display: none;
    }

    .big {
        width: 100%;
        height: 100%;
        position: absolute;
        top: -1px;
        left: 100%;
        border: 1px solid #aaa;
        overflow: hidden;
        z-index: 998;
        display: none;
        background: white;

        img {
            width: 200%;
            max-width: 200%;
            height: 200%;
            position: absolute;
            left: 0;
            top: 0;
        }
    }


    .event:hover ~ .mask,
    .event:hover ~ .big {
        display: block;
    }
}
</style>
