// 导入获取search页面数据的函数
import { reqSearchInfo } from "@/api/index";

const actions = {
    // search页面挂载后，派发actions，向后端服务区请求数据
    async getSearchList(context, params) {
        let results = await reqSearchInfo(params);
        if (results.code == 200) {
            context.commit("GETSEARCHLIST", results.data);
        }
        else if(err){
            console.log(err.message);
        }
        // else {
        //     console.log(results);
        // }
    },
};
const mutations = {
    GETSEARCHLIST(state, searchList) {
        state.searchList = searchList;
    },
};
const state = {
    // 根据服务器返回的数据，可知，searchList的初始值为对象形式
    searchList: {},
};
const getters = {
    // 通过getters简化searchList中的数据
    // 商品列表 goodsList
    goodsList(state) {
        //没网的情况下 state.searchList.goodsList 的值是undefined,为了防止报错,设置一个空数组以下同理
        return state.searchList.goodsList || [];
    },
    // 商品属性列表
    attrsList(state) {
        return state.searchList.attrsList || [];
    },
    // 商标列表
    trademarkList(state) {
        return state.searchList.trademarkList || [];
    },
    // 返回要分页渲染的数据总条数
    totalData(state) {
        return state.searchList.total;
    },
};

export default {
    namespaced: true,
    actions,
    mutations,
    state,
    getters,
};
