// 引入我们封装的向后端请求数据的函数
import { reqCategoryList, reqBannerList, reqFloorList } from "@/api/index";

const actions = {
    // 向后端发Ajax请求获取数据
    // getCategoryList(context){
    //     reqCategoryList().then((res)=>{
    //         const results = res.data
    //         context.commit('GetCategoryList',results)
    //     })
    // }
    // 商品列表数据（真实）
    async getCategoryList(context) {
        const results = await reqCategoryList();
        if (results.code == 200) {
            context.commit("GETCATEGORYLIST", results.data);
        }
    },

    // 获取首页轮播图数据（mock）
    async getBannerList(context) {
        const results = await reqBannerList();
        if (results.code == 200) {
            context.commit("GETBANNERLIST", results.data);
        }
    },
    // 获取首页 floor组件的数据 （mock）
    async getFloorList(context){
        const results = await reqFloorList()
        if(results.code == 200){
            context.commit("GETFLOORLIST",results.data)
        }
        
    },
};
const mutations = {
    // 商品列表
    GETCATEGORYLIST(state, results) {
        state.CategoryList = results;
    },
    // 首页轮播图（mock）
    GETBANNERLIST(state, results) {
        state.BannerList = results;
    },
    // 首页 floor组件的数据（mock）
    GETFLOORLIST(state, results){
        state.floorList = results
    },
};
const state = {
    CategoryList: [],
    BannerList: [],
    floorList: [],
};
const getters = {};

export default {
    namespaced: true,
    actions,
    mutations,
    state,
    getters,
};
