<template>
    <div class="pagination">
        <!-- 当前页码为1时，上一页就不能再被点击了 -->
        <!-- 当用户点击该按钮触发父组件自定义事件getPageNo,将当前页码减一传回父组件，并重新发请求渲染页面 -->
        <button :disabled="pageNo == 1" @click="$emit('getPageNo', pageNo - 1)">上一页</button>
        <!-- 当连续页码数的开始数大于1时就显示该按钮 -->
        <!-- 点击该按钮，固定传回下次当前页码为1 -->
        <button v-if="startAndEndNumber.startNum > 1" @click="$emit('getPageNo', 1)">1</button>
        <!-- 当连续页码数的开始数大于2时就显示该按钮 -->
        <button v-if="startAndEndNumber.startNum > 2">···</button>

        <!-- 注意：v-for 可以遍历 数组|对象|数字|字符串 -->
        <!-- 显示当前大于连续页码开始页数的按钮 -->
        <!-- 当pageNo和page相等时，用户在的这一页应该有一个标识 -->
        <button
            v-for="(page, index) in startAndEndNumber.endNum"
            :key="index"
            v-show="page >= startAndEndNumber.startNum"
            @click="$emit('getPageNo', page)"
            :class="{ showBlue: pageNo == page }"
        >
            {{ page }}
        </button>

        <!-- 当总页码数减去连续页码结束数大于1时 -->
        <button v-if="totalPage - startAndEndNumber.endNum > 1">···</button>
        <!-- 这是最后一页 当连续页码数的结束数小于总页数时就显示该按钮 -->
        <button v-if="startAndEndNumber.endNum < totalPage" @click="$emit('getPageNo', totalPage)">
            {{ totalPage }}
        </button>
        <!-- 当前页码为总页码数时，下一页就不能再被点击了 -->
        <button :disabled="pageNo == totalPage" @click="$emit('getPageNo', pageNo + 1)">
            下一页
        </button>
        <button style="margin-left: 30px">共 {{ totalData }} 条</button>
    </div>
</template>

<script>
export default {
    // 分页器组件
    name: "Pagination",
    props: ["pageNo", "pageSize", "continuousPage", "totalData"],
    computed: {
        // 可以计算出共多少页
        totalPage() {
            // 总数据条数除以每页显示数据条数，向上取整
            return Math.ceil(this.totalData / this.pageSize);
        },
        // 连续页码开始的数字和结束的数字
        startAndEndNumber() {
            // 初始化开始和结束的数字
            let startNum = 0;
            let endNum = 0;
            // 异常情况：当出现连续页码数大于页码总数的时候
            if (this.continuousPage > this.totalPage) {
                startNum = 1;
                endNum = this.totalPage;
            } else {
                // 正常清空下，计算连续页码范围
                startNum = this.pageNo - parseInt(this.continuousPage / 2);
                endNum = this.pageNo + parseInt(this.continuousPage / 2);
                // 纠正bug，比如当前页码pageNo为2或者1时，startNum会出现0或-1，而这不符合分页器常识 startNum不能小于1
                if (startNum < 1) {
                    startNum = 1;
                    endNum = this.continuousPage;
                }
                // 纠正bug,比如当前页码数pageNo和总页码数（最后一页）相同的时候，endNum的数字超出了总页码数，endNum不能大于totalPage
                if (endNum > this.totalPage) {
                    endNum = this.totalPage;
                    startNum = this.totalPage - this.continuousPage + 1;
                }
            }
            return { startNum, endNum };
        },
    },
};
</script>

<style lang="less" scoped>
.pagination {
    text-align: center;
    .showBlue {
        background: skyblue;
    }
    button {
        margin: 0 5px;
        background-color: #f4f4f5;
        color: #606266;
        outline: none;
        border-radius: 2px;
        padding: 0 4px;
        vertical-align: top;
        display: inline-block;
        font-size: 13px;
        min-width: 35.5px;
        height: 28px;
        line-height: 28px;
        cursor: pointer;
        box-sizing: border-box;
        text-align: center;
        border: 0;

        &[disabled] {
            color: #c0c4cc;
            cursor: not-allowed;
        }

        &.active {
            cursor: not-allowed;
            background-color: #409eff;
            color: #fff;
        }
    }
}
</style>
