// 封装一个生成一个唯一id且保存在本地存储，不会改变的函数
// 引入uuid，通过as起别名 （在Vue2脚手架项目中已经有uuid了，不需要再下载安装） 生成唯一id（字符串）
import { v4 as uuidv4 } from "uuid";
export function getUUID() {
    // 获取本地存储中的UUIDTOKEN
    let uuid_token = localStorage.getItem("UUIDTOKEN");
    // 判断本地存储中是否已经有UUIDTOKEN，没有新生成，有的话直接返回
    if (!uuid_token) {
        // 没有的话，生成一个
        uuid_token = uuidv4();
        // 并将其存储在本地存储
        localStorage.setItem("UUIDTOKEN", uuid_token);
    }
    // 将uuid_token作为返回值返回
    return uuid_token;
}
