<template>
    <!-- 头部 -->
    <header class="header">
        <!-- 头部的第一行 -->
        <div class="top">
            <div class="container">
                <div class="loginList">
                    <p>尚品汇欢迎您！</p>
                    <!-- 未登录，不显示用户名 -->
                    <p v-if="!username">
                        <span>请</span>
                        <router-link to="/login">登录</router-link>
                        <router-link class="register" to="/register">免费注册</router-link>
                    </p>
                    <!-- 成功登录，显示用户名 -->
                    <p v-if="username">
                        <a>{{username}}</a>
                        <a class="register" @click="userLogout">退出登录</a>
                    </p>
                </div>
                <div class="typeList">
                    <router-link to="/center/myorder">我的订单</router-link>
                    <router-link to="/shopcart">我的购物车</router-link>
                    <a href="###">我的尚品汇</a>
                    <a href="###">尚品汇会员</a>
                    <a href="###">企业采购</a>
                    <a href="###">关注尚品汇</a>
                    <a href="###">合作招商</a>
                    <a href="###">商家后台</a>
                </div>
            </div>
        </div>
        <!--头部第二行 搜索区域-->
        <div class="bottom">
            <h1 class="logoArea">
                <router-link class="logo" to="/home">
                    <img src="./images/logo.png" alt="" />
                </router-link>
            </h1>
            <div class="searchArea">
                <form action="###" class="searchForm">
                    <input
                        type="text"
                        id="autocomplete"
                        class="input-error input-xxlarge"
                        v-model="keyword"
                    />
                    <button class="sui-btn btn-xlarge btn-danger" type="button" @click="goSearch">
                        搜索
                    </button>
                </form>
            </div>
        </div>
    </header>
</template>

<script>
export default {
    name: "Header",
    data() {
        return {
            keyword: "",
        };
    },
    methods: {
        // 跳转到search页，通过push方式
        goSearch() {
            let location = {
                name: "search",
                params: {
                    keyword: this.keyword || undefined,
                },
            };
            // 请求的query参数和params参数合并
            if (this.$route.query.categoryName) {
                location.query = this.$route.query;
            }

            this.$router.push(
                /* {
                    name: "search",
                    // 携带参数
                    // query: {
                    //     searchKeyword: this.searchKeyword,
                    // },
                    // 传递params参数，必须要通过name的方式进行跳转
                    params: {
                        //    searchKeyword: ""
                        //当params参数可传可不传的时候，传递空串的话，会对url路径产生影响
                        // 解决方法: 使用undefined
                        //    searchKeyword: "" || undefined
                        searchKeyword: this.searchKeyword || undefined,
                    },
                }, */
                location,
                //产生NavigationDuplicated是因为调用push 返回的是一个promise ，它需要传递一个成功或者失败的回调
                // 可以通过以下方式解决，给他传递回调函数，但是这种方式治标不治本，可以对push或者replace进行二次封装
                () => {},
                () => {
                    // console.log(error.message);
                }
            );
        },
        // 用户退出登录
        userLogout(){
            this.$store
            .dispatch("user/userLogout")
            .then(()=>{})
            .catch(()=>{})
        }
    },
    computed: {
        // 用户登录成功后的用户名
        username() {
            return this.$store.state.user.userInfo.name;
        },
    },
    mounted() {
        // search组件中，我们点击面包屑，删除关键字时触发全局事件总线,清空搜索框里的信息
        // 这里为了让this指向组件实例对象，采用箭头函数的形式
        this.$bus.$on("clearKeyword", () => {
            this.keyword = "";
        });
    },
    beforeDestroy() {
        // 解绑全局事件总线
        this.$bus.$off("clearKeyword");
    },
};
</script>

<style lang="less" scoped>
.header {
    & > .top {
        background-color: #eaeaea;
        height: 30px;
        line-height: 30px;

        .container {
            width: 1200px;
            margin: 0 auto;
            overflow: hidden;

            .loginList {
                float: left;

                p {
                    float: left;
                    margin-right: 10px;

                    .register {
                        border-left: 1px solid #b3aeae;
                        padding: 0 5px;
                        margin-left: 5px;
                    }
                }
            }

            .typeList {
                float: right;

                a {
                    padding: 0 10px;

                    & + a {
                        border-left: 1px solid #b3aeae;
                    }
                }
            }
        }
    }

    & > .bottom {
        width: 1200px;
        margin: 0 auto;
        overflow: hidden;

        .logoArea {
            float: left;

            .logo {
                img {
                    width: 175px;
                    margin: 25px 45px;
                }
            }
        }

        .searchArea {
            float: right;
            margin-top: 35px;

            .searchForm {
                overflow: hidden;

                input {
                    box-sizing: border-box;
                    width: 490px;
                    height: 32px;
                    padding: 0px 4px;
                    border: 2px solid #ea4a36;
                    float: left;

                    &:focus {
                        outline: none;
                    }
                }

                button {
                    height: 32px;
                    width: 68px;
                    background-color: #ea4a36;
                    border: none;
                    color: #fff;
                    float: left;
                    cursor: pointer;

                    &:focus {
                        outline: none;
                    }
                }
            }
        }
    }
}
</style>
