import VueRouter from "vue-router";

// 引入路由配置项信息模块】
import routes from "./routes";

// 在该模块引入Vuex仓库模块即 store
// 主要用于获取，用户登录的相关信息和token信息
import store from "@/store";

// 对push和replace进行二次封装
// 保存一份源push和源replace
const originpush = VueRouter.prototype.push;
const originreplace = VueRouter.prototype.replace;

// 改写push
// location:路由进行跳转的信息
VueRouter.prototype.push = function (location, onComplete, onAbort) {
    if (onComplete && onAbort) {
        originpush.call(this, location, onComplete, onAbort);
    } else {
        originpush.call(
            this,
            location,
            () => {},
            () => {}
        );
    }
};
// 改写replace
VueRouter.prototype.replace = function (location, onComplete, onAbort) {
    if (onComplete && onAbort) {
        originreplace.call(this, location, onComplete, onAbort);
    } else {
        originreplace.call(
            this,
            location,
            () => {},
            () => {}
        );
    }
};

// 配置路由
let router = new VueRouter({
    // 简写形式
    routes,
    /* 使用前端路由，当切换到新路由时，想要页面滚到顶部，或者是保持原先的滚动位置，
    就像重新加载页面那样。它让你可以自定义路由切换时页面如何滚动。 */
    scrollBehavior(to, from, savedPosition) {
        // 始终滚动到顶部
        // x控制水平，y控制垂直
        // return { x : 0 ,y : 0 }
        return { y: 0 };
    },
});

// 全局前置路由守卫：
router.beforeEach((to, from, next) => {
    // 1.先判读用户是否登录 （是否有token）
    let token = store.state.user.token; // 仓库中的token是否存在(登录成功才有)
    let name = store.state.user.userInfo.name; //  仓库中是否有用户登录成功后的信息中的用户名（请求头携带token，同时发Ajax请求成功后才有）
    if (token) {
        // 有token，表示已经成功登录了
        if (to.path == "/login") {
            // 登录成功后，用户不应该还能跳转到登录页面，让还想跳转到登录页面时，放行到home路由
            next({ name: "home" });
            // console.log("登录成功但是访问的是登录页面，放行到home");
        } else {
            // 登录成功，用户跳转到除了 login 外的页面时 列如：home|search|detail|shopcart|
            if (name) {
                // 如果有用户登录信息了就不在需要，派发通过token获取用户登录信息的action了，Header组件直接就能展示，使用直接放行
                next();
                // console.log("登录成功，不是访问登录，有name直接放行");
            } else {
                // 没有用户登录信息，需要重新通过token请求数据
                store
                    .dispatch("user/getUserInfoByToken")
                    .then(() => {
                        // 获取用户登录信息成功,放行
                        next();
                        // console.log("登录成功，不是访问登录，但是没有name，派发action成功后在放行");
                    })
                    .catch(() => {
                        // 获取用户登录信息失败，为什么失败？ 服务器先前下发的token过期了
                        // 我们需要清除现在的token，并跳转到登录页面，（在登录的时候会获取新的token）
                        store.dispatch("user/userLogout");
                        next({ name: "login" });
                        // console.log("token失效");
                    });
            }
        }
    } else {
        // 路由的路径
        let topath = to.path;
        // 没有token，表示未登录
        // 未登录：不能去的页面： 交易确认订单页面|支付页面|支付成功|我的订单页面
        if (
            topath.indexOf("/trade") !== -1 ||
            topath.indexOf("/pay") !== -1 ||
            topath.indexOf("/paysuccess") !== -1 ||
            topath.indexOf("/center") !== -1
        ) {
            // 未登录，去确认交易页面,放行到登录页面
            alert("请您登录后在操作");
            // next({
            //     name: "login",
            // });

            // path写法
            // next("/login")
            // 可以在路由跳转的时候将想要去的路由路径信息存储在地址栏【即路由中的query参数 例如 ：redirect:"/center/myorder" 】
            next(`/login?redirect=${topath}`)
        } else {
            // 去其它不需要登录的页面，直接放行
            next();
        }
    }
});

export default router;
