// 用户登录与注册的仓库

// 引入请求验证码的函数|用户登录的函数
import { reqAuthCode, reqRegUser, reqUserLogin, reqUserLogout ,reqUserInfoByToken} from "@/api/index";

// 引入存储 token 工具函数
import { setToken, getToken,removeToken } from "@/utils/token";
const actions = {
    // 获取验证码
    async getCode(context, phone) {
        let results = await reqAuthCode(phone);
        if (results.code) {
            context.commit("GETCODE", results.data);
            return "ok";
        } else {
            return Promise.reject(new Error("获取验证码失败"));
        }
    },
    // 注册用户
    async regUser(context, data) {
        let results = await reqRegUser(data);
        // console.log(results);
        if (results.code == 200) {
            return "ok";
        } else {
            return Promise.reject(new Error("注册失败"));
        }
    },
    // 用户登录
    async userLogin(context, data) {
        let results = await reqUserLogin(data);
        // 测试账号： 1765767112  56789
        // console.log(results); //注意：这里返回的用户信息详情，实际项目一般只会返回token（令牌通证）
        // 而且 vuex 仓库中的数据，不会持久保存（刷新网页就没了）
        if (results.code == 200) {
            // 到这里，用户登录成功，获取到了token
            context.commit("USERLOGIN", results.data.token);
            // 持久化存储token
            setToken(results.data.token); //这是封装存储token的函数
            return "ok";
        } else {
            return Promise.reject(results.message);
        }
    },
    // token校验获取用户登录信息（登录成功后有token，请求时在请求头中提交token）
    async getUserInfoByToken(context) {
        let results = await reqUserInfoByToken();
        if (results.code == 200) {
            context.commit("GETUSERINFOBYTOKEN", results.data);
            return "ok"
        }else{
            return Promise.reject(new Error("faile"))
        }
    },
    // 用户退出登录
    async userLogout(context) {
        let results = await reqUserLogout();
        if (results.code == 200) {
            // 到这里表示服务器已经清楚了用户在数据库的一些信息，列如token
            context.commit("USERLOGOUT");
            // 提交commit清除本地用户数据，包括vuex仓库中的数据和localStorage中的相关数据
            return "ok"
        }else{
            return Promise.reject(new Error("faile"))
        }
    },
};
const mutations = {
    // 获取验证码
    GETCODE(state, code) {
        state.code = code;
    },
    // 用户登录服务器下发token
    USERLOGIN(state, token) {
        state.token = token;
    },
    // 登录成功的用户信息
    GETUSERINFOBYTOKEN(state, userInfo) {
        state.userInfo = userInfo;
    },
    // 用户退出登录,清除本地相关数据
    USERLOGOUT(state) {
        // 清除仓库中的用户相关的数据
        state.token = "";
        state.userInfo = {}
        // 清除 localStorage 中存储的相关数据,以下是封装的清除localStorage中token的函数
        removeToken()
    },
};
const state = {
    // 验证码
    code: "",
    // 通证
    // 在用户没有成功登录的时候，我们是无法获取到TOKEN的，这时token的值为null
    /* 在用户成功登录的时候，我们通过setToken函数对服务器下发的token进行了本地存储，
     我们是可以获取到TOKEN的，这时token的值为服务器返回的token */
    token: getToken(), //这是封装获取本地token的函数
    // 在登录成功后存储好token，方便在请求拦截器中获取到存储的token（在发通过请求头携带token的Ajax请求时，该token）

    // 登录成功通过在请求头中添加token获得的用户登录信息，初始化对象
    userInfo: {},
};
const getters = {};

export default {
    namespaced: true,
    actions,
    mutations,
    state,
    getters,
};
