import Vue from "vue";
import App from "./App.vue";

// 引入VueRouter
import VueRouter from "vue-router";
//  引入router配置项
import router from "./router/router";
// 引入store配置项
import store from "./store";

// 引入图片懒加载插件库
import VueLazyload from "vue-lazyload";

// 关闭生产提示
Vue.config.productionTip = false;

// 应用插件 路由
Vue.use(VueRouter);
// 应用图片懒加载插件
// 引入服务器数据还未返回，图片懒加载时显示的图片
import loadimage from "@/assets/lazy.gif";
// import loadimage from "@/assets/lazy.jpg";
Vue.use(VueLazyload, {   loading: loadimage }); //应用后多了 一个自定义指令 v-lazy 可以代替 img标签中的 :src

import "@/plugins/validate" //让表单验证的模块至少执行一次

// 注意全局组件直接使用，不需要再进行引入
// TypeNav组件在Home，Search，Detail页面都有使用，所以将该组件注册为全局组件
import TypeNav from "@/components/TypeNav";
Vue.component(TypeNav.name, TypeNav);

// 将Carousel注册为全局组件（轮播图）
import Carousel from "@/components/Carousel";
Vue.component(Carousel.name, Carousel);

// 将Pagination组件（分页器）在search页面和详情页面都有使用，所以将其注册为全局组件
import Pagination from "@/components/Pagination";
Vue.component(Pagination.name, Pagination);

// 按需引入ElementUI组件库
import { MessageBox } from "element-ui";
// 以下是 element-ui 在注册组件时的一种写法，挂载在Vue原型上
Vue.prototype.$msgbox = MessageBox;
Vue.prototype.$alert = MessageBox.alert;

// 引入mock数据 ，至少让他执行一次
import "@/mock/mockServe.js";

// 引入swiper样式
import "swiper/css/swiper.css";

// 引入发Ajax请求的模块( * 表示导出所有元素 用 as 命名别名为 API)
import * as API from "@/api";

new Vue({
    el: "#app",
    render: (h) => h(App),

    beforeCreate() {
        // 注册全局事件总线,这样所有组件实例上就多了一个$bus
        Vue.prototype.$bus = this;
        // 和全局事件总线原理一样，让所有组件实例都能访问到api文件里的Ajax请求函数,
        // 通过Vue.prototype原型对象,将全部请求函数挂载到原型对象身上[VC:就可以使用请求函数]
        Vue.prototype.$API = API;
    },
    // 配置router项（简写）
    // 这样每个组件上就多了一个$router(可以调用push,replace等方法) , $route(路由name，path，query，params等数据)
    router,
    // 配置store项（简写）
    // 这样组件上就多了一个$store（可以调用dispatch,commit方法）
    store,
});
