<template>
    <!-- 我的订单 -->
    <div class="order-content">
        <div class="title">
            <h3>我的订单</h3>
        </div>
        <!-- 表头 -->
        <div class="chosetype">
            <table>
                <thead>
                    <tr>
                        <th width="29%">商品</th>
                        <th width="31%">订单详情</th>
                        <th width="13%">收货人</th>
                        <th>金额</th>
                        <th>状态</th>
                        <th>操作</th>
                    </tr>
                </thead>
            </table>
        </div>
        <!-- 商品订单详情 -->
        <div class="orders">
            <!-- 每页显示多少条数据（显示几个订单）就遍历几次 -->
            <table class="order-item" v-for="order in myOrderList.records" :key="order.id">
                <!-- 订单时间即订单号 -->
                <thead>
                    <tr>
                        <th colspan="5">
                            <span class="ordertitle"
                                >{{ order.createTime }} 订单编号：{{ order.outTradeNo }}
                                <span class="pull-right delete"
                                    ><img src="../images/delete.png" /></span
                            ></span>
                        </th>
                    </tr>
                </thead>

                <tbody>
                    <!-- 每个订单购买的同种商品数量是多少就遍历几次 -->
                    <tr v-for="(cart, index) in order.orderDetailList" :key="cart.id">
                        <td width="60%">
                            <div class="typographic">
                                <img :src="cart.imgUrl" style="wight: 82px; height: 82px" />
                                <a href="#" class="block-text">{{ cart.skuName }}</a>
                                <span>x{{ cart.skuNum }}</span>
                                <a href="#" class="service">售后申请</a>
                            </div>
                        </td>
                        <!-- 动态的合并表格行，这取决于每个订单中购买同种商品的数量 -->
                        <!-- 由于需要合并行，所以我们只需要订单中第一个商品的信息（收件人|支付总金额|支付状态）就够了 -->
                        <td
                            :rowspan="order.orderDetailList.length"
                            width="8%"
                            class="center"
                            v-if="index == 0"
                        >
                            {{ order.consignee }}
                        </td>
                        <td
                            :rowspan="order.orderDetailList.length"
                            width="13%"
                            class="center"
                            v-if="index == 0"
                        >
                            <ul class="unstyled">
                                <li>总金额¥{{ order.totalAmount }}.00</li>
                                <li>在线支付</li>
                            </ul>
                        </td>
                        <td
                            :rowspan="order.orderDetailList.length"
                            width="8%"
                            class="center"
                            v-if="index == 0"
                        >
                            <a href="#" class="btn">{{ order.orderStatusName }} </a>
                        </td>
                        <td
                            :rowspan="order.orderDetailList.length"
                            width="13%"
                            class="center"
                            v-if="index == 0"
                        >
                            <ul class="unstyled">
                                <li>
                                    <a href="mycomment.html" target="_blank">评价|晒单</a>
                                </li>
                            </ul>
                        </td>
                    </tr>
                </tbody>
            </table>
        </div>
        <!-- 分页器:改用我们的 全局组件 Pagination -->
        <!-- pageNo:当前页码 | pageSize：当前显示多少条数据 | continuousPage：连续页码数 | 
        totalData：总共多少条数据 | 绑定自定义事件getPageNo：获取当前页(在分页器组件触发该事件) -->
        <Pagination
            :pageNo="page"
            :pageSize="limit"
            :continuousPage="5"
            :totalData="myOrderList.total"
            @getPageNo="getPageNo"
        />
    </div>
</template>

<script>
export default {
    name: "MyOrder",
    data() {
        return {
            page: "1", //当前页码，初始化为第一页
            limit: "3", //每页展示多少条数据，初始化每页展示三条数据
            // 我的订单列表信息（存储|展示）初始化为空对象
            myOrderList: {},
        };
    },
    mounted() {
        // 挂载，发请求，获取我的订单列表数据
        // 调用封装的请求我的订单列表数据函数
        this.getMyOrderList();
    },
    methods: {
        // 封装的请求 我的订单列表数据 函数
        async getMyOrderList() {
            // 解构赋值
            let { page, limit } = this;
            let results = await this.$API.reqMyOrderList(page, limit);
            if (results.code == 200) {
                // 请求我的订单列表信息成功 ， 进行存储
                this.myOrderList = results.data;
            }
        },
        // 点击分页器页码触发自定义事件 getPageNo 它的回调函数也叫getPageNo 将当前页码改为用户点击的页码+并重新发请求获取新数据
        getPageNo(page){
            this.page = page
            this.getMyOrderList();
        }
    },
};
</script>
