// 购物车页面的数据仓库

// 引入我们封装的向后端请求数据的函数
import { reqShopcartInfo, reqDeleteShopcartListById, reqChangeShopcartChecked } from "@/api/index";
const actions = {
    // 向服务器请求购物车列表数据
    async getShopcartList(context) {
        let results = await reqShopcartInfo();
        //在不知道用户id（游客id或注册id）的情况下，虽然可以请求成功，但是没有相应的数据返回级results.data是一个空数组
        // 对于游客，我们应该自动生成一个id，可以通过uuid实现，在详情页面保存生成的游客id，通过store模块暴露和引入，
        // 可以让api中的requests.js模块（发Ajax请求的模块）获取到游客id
        // 并通过请求头的方式，将游客id传给服务器（需要提前与后端商量好请求头的字段）
        // 如果请求成功，commit，修改state中的数据
        if (results.code == 200) {
            context.commit("GETSHOPCARTLIST", results.data);
        }
    },
    // 通过商品id删除购物车中的某一项商品
    async DeleteShopcartById(context, skuId) {
        let results = await reqDeleteShopcartListById(skuId);
        // console.log(results);
        // 如果请求成功，返回成功的信息
        if (results.code == 200) {
            return Promise.resolve("ok");
        } else {
            // 请求失败
            return Promise.reject(new Error("删除失败"));
        }
    },
    // 通过商品id改变购物车页面的商品的选中状态
    async changeShopcartChecked(context, { skuId, isChecked }) {
        let results = await reqChangeShopcartChecked(skuId, isChecked);
        // console.log(results);
        if (results.code == 200) {
            return "ok";
        } else {
            return Promise.reject(new Error("选中错误"));
        }
    },
    // 删除全部的被选中的商品
    deleteAllShopCartChecked(context, cartInfoList) {
        // 准备一个空数组用来存储派发DeleteShopcartById返回的promise
        let promiseAll = [];
        // 注意context中包含：
        // 【commit：提交mutations，修改state中的数据】，【dispatch：派发action】，【getters：当前仓库的计算属性】，【state：当前数据小仓库】
        cartInfoList.forEach((element) => {
            // 我们可以在这里再次派发action(通过商品id单次删除商品)
            // let results = context.dispatch("DeleteShopcartById", element.skuId);
            // console.log(results); //返回的是一个promise

            // 筛选被选中的商品删除
            let results = element.isChecked ? context.dispatch("DeleteShopcartById", element.skuId) : "";
            promiseAll.push(results);
        });
        // 注释：Promise.all([p1,p2,p3])，
        // p1,p2,p3都是promise，如果有一个promise失败，都失败，如果p1,p2,p3都成功，Promise.all([p1,p2,p3])返回成功
        // console.log(promiseAll);
        // console.log(Promise.all(promiseAll));
        return Promise.all(promiseAll); //如果分别都请求成功（每一个单个删除成功），返回成功
    },
};
const mutations = {
    GETSHOPCARTLIST(state, cartList) {
        state.cartList = cartList;
    },
};
const state = {
    // 服务器返回的cartList为数组，所以初始为空数组
    cartList: [],
};
const getters = {
    // 简化返回的数据
    cartInfoObj(state) {
        // 至少返回的是一个空对象，才不会报错
        return state.cartList[0] || {};
    },
};

export default {
    namespaced: true,
    actions,
    mutations,
    state,
    getters,
};
