<template>
    <div class="pay-main">
        <div class="pay-container">
            <div class="checkout-tit">
                <h4 class="tit-txt">
                    <span class="success-icon"></span>
                    <span class="success-info">订单提交成功，请您及时付款，以便尽快为您发货~~</span>
                </h4>
                <div class="paymark">
                    <span class="fl"
                        >请您在提交订单<em class="orange time">4小时</em
                        >之内完成支付，超时订单会自动取消。订单号：<em>{{ orderId }}</em></span
                    >
                    <span class="fr"
                        ><em class="lead">应付金额：</em
                        ><em class="orange money">￥{{ orderPayInfo.totalFee }}</em></span
                    >
                </div>
            </div>
            <div class="checkout-info">
                <h4>重要说明：</h4>
                <ol>
                    <li>尚品汇商城支付平台目前支持<span class="zfb">支付宝</span>支付方式。</li>
                    <li>其它支付渠道正在调试中，敬请期待。</li>
                    <li>为了保证您的购物支付流程顺利完成，请保存以下支付宝信息。</li>
                </ol>
                <h4>支付宝账户信息：（很重要，<span class="save">请保存！！！</span>）</h4>
                <ul>
                    <li>支付帐号：11111111</li>
                    <li>密码：111111</li>
                    <li>支付密码：111111</li>
                </ul>
            </div>
            <div class="checkout-steps">
                <div class="step-tit">
                    <h5>支付平台</h5>
                </div>
                <div class="step-cont">
                    <ul class="payType">
                        <li><img src="./images/pay2.jpg" /></li>
                        <li><img src="./images/pay3.jpg" /></li>
                    </ul>
                </div>
                <div class="hr"></div>

                <div class="payshipInfo">
                    <div class="step-tit">
                        <h5>支付网银</h5>
                    </div>
                    <div class="step-cont">
                        <ul class="payType">
                            <li><img src="./images/pay10.jpg" /></li>
                            <li><img src="./images/pay11.jpg" /></li>
                            <li><img src="./images/pay12.jpg" /></li>
                            <li><img src="./images/pay13.jpg" /></li>
                            <li><img src="./images/pay14.jpg" /></li>
                            <li><img src="./images/pay15.jpg" /></li>
                            <li><img src="./images/pay16.jpg" /></li>
                            <li><img src="./images/pay17.jpg" /></li>
                            <li><img src="./images/pay18.jpg" /></li>
                            <li><img src="./images/pay19.jpg" /></li>
                            <li><img src="./images/pay20.jpg" /></li>
                            <li><img src="./images/pay21.jpg" /></li>
                            <li><img src="./images/pay22.jpg" /></li>
                        </ul>
                    </div>
                </div>
                <div class="hr"></div>

                <div class="submit">
                    <!-- <router-link class="btn" to="/paysuccess">立即支付</router-link> -->
                    <!-- 点击跳出支付提示框 -->
                    <a class="btn" @click="open">立即支付</a>
                </div>
                <div class="otherpay">
                    <div class="step-tit">
                        <h5>其他支付方式</h5>
                    </div>
                    <div class="step-cont">
                        <span><a href="weixinpay.html" target="_blank">微信支付</a></span>
                        <span>中国银联</span>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
// 引入 QRCode （一个解析url字符串生成二维码图片的包）
import QRCode from "qrcode";
export default {
    name: "Pay",
    data() {
        return {
            // 订单支付信息，初始化为空对象
            orderPayInfo: {},
            // 持续获取支付状态 循环计时器的返回值 初始值为null
            timer: null,
            // 存储服务器返回的状态码，用于验证，是否真的支付成功
            code: "",
        };
    },
    mounted() {
        // 挂载，发请求获取订单支付信息
        // 注意：最好不要在生命周期函数中使用 async | await
        this.getOrderPayInfo();
    },
    methods: {
        // 请求订单支付信息
        async getOrderPayInfo() {
            let { orderId } = this; //解构赋值
            let results = await this.$API.reqOrderPayInfo(orderId);
            // console.log(results);
            if (results.code == 200) {
                // 请求订单支付信息成功
                // 获取服务器返回的订单支付信息
                this.orderPayInfo = results.data;
            } else {
                // 请求失败，提醒用户
                alert(results.message);
            }
        },
        // element-ui 组件 点击弹出支付二维码提示框
        async open() {
            // 生成二维码 通过QRCode包 url:生成二维码的地址
            let url = await QRCode.toDataURL(this.orderPayInfo.codeUrl);
            this.$alert(`<img src=${url} />`, "请扫描微信二维码", {
                //是否将 message（  "<strong>这是 <i>HTML</i> 片段</strong>"  ） 属性作为 HTML 片段处理
                dangerouslyUseHTMLString: true,
                center: true, //是否居中布局
                showClose: false, // MessageBox 是否显示右上角关闭按钮
                showCancelButton: true, //是否显示取消按钮
                cancelButtonText: "支付遇见了问题", //取消按钮的文本内容
                confirmButtonText: "支付成功了", //确定按钮的文本内容
                closeOnClickModal: true, //点击遮罩层关闭messagebox
                // 在提示框关闭之前的回调函数
                beforeClose: (action, instance, done) => {
                    // action : 点击取消按钮（ cancel ） 点击确认按钮（ confirm ）
                    // instance：该组件实例
                    // done：关闭提示框函数
                    if (action == "cancel") {
                        // 点击取消按钮时（支付遇到了状况） 提醒用户联系管理员|关闭提示框|关闭循环定时器的支付状态请求
                        alert("请联系管理员处理"); //提醒用户联系管理员
                        clearInterval(this.timer); //关闭循环定时器的支付状态请求
                        this.timer = null;
                        done(); //关闭提示框
                    } else {
                        // 点击的不是取消按钮（支付成功） 判断状态码为200才是真的支付成功
                        // if (this.code == 200) {
                        // 以下是一个后门：this.code == 200才代表请求订单支付信息成功，为了方便开发直接改为205
                        if (this.code == 205) {
                            // 真的支付成功了
                            clearInterval(this.timer); //关闭循环定时器的支付状态请求
                            this.timer = null;
                            done(); //关闭提示框
                            // 跳转到支付成功页面
                            this.$router.push({
                                name: "paySuccess",
                            });
                        }
                    }
                },
            })
                // 显示取消按钮，要提供 成功|失败 的回调，因为返回的是一个promise
                .then(() => {})
                .catch(() => {});
            // 需要知道是否支付成功
            // 支付成功路由跳转，支付失败弹出信息提示用户
            // 如果没有定时器开启，我们开启一个定时器来循环获取支付状态
            if (!this.timer) {
                // 发请求获取支付状态（支付中、支付成功、支付失败）(需要持续请求获取动态的支付状态)
                this.timer = setInterval(async () => {
                    let results = await this.$API.reqPayStatus(this.orderId);
                    console.log(results);
                    if (results.code == 200) {
                        // 存储服务器返回的状态码，用于验证，是否真的支付成功
                        this.code = results.code;
                        // 支付成功: 关闭循环定时器|初始化timer|跳转到支付成功页面|关闭支付提示框
                        // 关闭循环定时器
                        clearInterval(this.timer);
                        // 初始化timer
                        this.timer = null;
                        // 关闭支付提示框
                        this.$msgbox.close();
                        // 跳转到支付成功页面
                        this.$router.push({
                            name: "paySuccess",
                        });
                    } else {
                        // 支付未成功 存储服务器返回的状态码，用于验证，是否真的支付成功
                        this.code = results.code;
                    }
                }, 1000);
            }
        },
    },
    computed: {
        // 订单号
        orderId() {
            return this.$route.query.orderId;
        },
    },
};
</script>

<style lang="less" scoped>
.pay-main {
    margin-bottom: 20px;

    .pay-container {
        margin: 0 auto;
        width: 1200px;

        a:hover {
            color: #4cb9fc;
        }

        .orange {
            color: #e1251b;
        }

        .money {
            font-size: 18px;
        }

        .zfb {
            color: #e1251b;
            font-weight: 700;
        }

        .checkout-tit {
            padding: 10px;

            .tit-txt {
                font-size: 14px;
                line-height: 21px;

                .success-icon {
                    width: 30px;
