//detail也Vuex仓库

//引入向服务器请求detail页商品详情数据的，Ajax请求函数 | 添加商品进入购物车，让服务器储存信息
import { reqGoodsInfo, reqAddorUpdateShopcart } from "@/api/index";

// 引入生成唯一游客id的函数（生成后不能再变了）
import { getUUID } from "@/utils/uuid_token";

const actions = {
    // 获取商品详情
    async getGoodsInfo(context, skuId) {
        let results = await reqGoodsInfo(skuId);
        // console.log(results);
        if (results.code) {
            context.commit("GETGOODSINFO", results.data);
        }
    },
    // 添加购买的商品进入购物车 | 更新购买商品, 将购买信息在服务器进行储存，并获取服务器是否添加成功的信息  注意：解构赋值
    async getAddorUpdateShopcartInfo(context, { skuId, skuNum }) {
        // console.log(skuId, skuNum);
        let results = await reqAddorUpdateShopcart(skuId, skuNum);
        // console.log(results);  //成功会返回results.code为200
        if (results.code == 200) {
            return "ok";
        } else {
            return Promise.reject(new Error("加入购物车失败"));
        }
    },
};
const mutations = {
    GETGOODSINFO(state, goodsInfo) {
        state.goodsInfo = goodsInfo;
        // console.log(goodsInfo);
    },
};
const state = {
    // 初始化商品详情数据为空对象(接口文档)
    goodsInfo: {},
    // 游客id（不能多变）
    uuid_token: getUUID(),
};
// 简化数据
const getters = {
    // 导航路径
    categoryView() {
        // 传入空对象防止假报错，因为在异步Ajax请求服务器数据回来前，goodsInfo还是一个空对象，他没有categoryView属性，会报undefined
        return state.goodsInfo.categoryView || {};
    },
    // 库存商品详情信息
    skuInfo() {
        return state.goodsInfo.skuInfo || {};
    },
    // 该商品的属性选择信息，例如颜色，内存容量等
    spuSaleAttrList() {
        // 空数组防止报错
        return state.goodsInfo.spuSaleAttrList || [];
    },
};

export default {
    namespaced: true,
    actions,
    mutations,
    state,
    getters,
};
