// 该模块是有关token的工具函数

// 存储用户登录成功token
export const setToken = (token) => {
    localStorage.setItem("TOKEN",token)
};
// 获取用户登录成功存储在本地的token
export const getToken = () => {
    return localStorage.getItem("TOKEN")
};
// 清除token
export const removeToken = () => {
     localStorage.removeItem("TOKEN")
};