// 这个模块：对API进行统一管理
import requests from "./requests";

// 引入发送mock请求的模块(模拟数据)
import MockRequests from "./MockRequests";

// 新接口：http://gmall-h5-api.atguigu.cn

// 首页三级分类
// 请求路径/api/product/getBaseCategoryList   方式get 不需要提供参数
export const reqCategoryList = () => {
    return requests({
        // 注意我们在二次封装axios的requests.js模块中，已经设置了  baseURL: "/api" 所以我们这里不要在添加 /api
        url: "/product/getBaseCategoryList",
        method: "get",
    });
};

// 首页轮播图 banner  （mock数据）
export const reqBannerList = () => {
    return MockRequests({
        url: "/banner",
        method: "get",
    });
};

// 首页floor 组件数据  （mock数据）
export const reqFloorList = () => {
    return MockRequests({
        url: "/floor",
        method: "get",
    });
};

// 获取search页面的数据
// 请求路径/api/list   方式：post   需要提供参数例子：
/* {
    "category3Id": "61",
    "categoryName": "手机",
    "keyword": "小米",
    "order": "1:desc",
    "pageNo": 1,
    "pageSize": 10,
    "props": ["1:1700-2799:价格", "2:6.65-6.74英寸:屏幕尺寸"],
    "trademark": "4:小米"
  } */
//   调用这个函数时，至少传入一个空对象
export const reqSearchInfo = (params) => {
    return requests({
        url: "/list",
        method: "post",
        data: params,
    });
};

// 获取detail页面的商品详情数据,要传入skuId参数
// 请求路径：/api/item/{ skuId }  请求方式：get  请求参数：skuId（商品ID） 为string类型且必选
export const reqGoodsInfo = (skuId) => {
    return requests({
        url: `/item/${skuId}`,
        method: "get",
    });
};

// 将购买的商品添加到购物车(对已有物品进行数量改动)
// 请求路径: /api/cart/addToCart/{ skuId }/{ skuNum } 请求方式：post
// 请求参数： skuId，string，必选（商品ID）| skuNum，string，必选（商品数量 正数代表增加 负数代表减少）
export const reqAddorUpdateShopcart = (skuId, skuNum) => {
    return requests({
        url: `/cart/addToCart/${skuId}/${skuNum}`,
        method: "post",
    });
};

//获取购物车列表
// 请求地址: /api/cart/cartList 请求方式:GET 不需要携带参数
export const reqShopcartInfo = () => {
    return requests({
        url: "/cart/cartList",
        method: "get",
    });
};

//删除购物车商品(通过商品的id即skuId)
// 请求地址 /api/cart/deleteCart/{skuId}  请求方式 DELETE  需要传参:skuId string 必选（商品id）
export const reqDeleteShopcartListById = (skuId) => {
    return requests({
        url: `/cart/deleteCart/${skuId}`,
        method: "delete",
    });
};

// 切换商品选中状态（ 通过 商品id | 当前的选中状态 )
// 请求地址：/api/cart/checkCart/{skuId}/{isChecked}  请求方式：GET
// 请求要携带的参数：
// 1. skuId	string	必选	商品ID
// 2. isChecked	string	必选	商品选中状态 0代表取消选中 1代表选中
export const reqChangeShopcartChecked = (skuId, isChecked) => {
    return requests({
        url: `/cart/checkCart/${skuId}/${isChecked}`,
        method: "get",
    });
};

//注册页面，获取验证码
// 请求地址：/api/user/passport/sendCode/{phone}  请求方式：get  携带参数：phone，必选
export const reqAuthCode = (phone) => {
    return requests({
        url: `/user/passport/sendCode/${phone}`,
        method: "get",
    });
};

// 注册用户
// 请求地址 /api/user/passport/register  请求方式 POST
// phone	string	必选	注册手机号
// password	string	必选	密码
// code	string	必选	验证码
export const reqRegUser = (data) => {
    return requests({
        url: "/user/passport/register",
        method: "post",
        data, // 简写
    });
};

// 用户登录
// 请求地址 /api/user/passport/login  请求方式 POST
// phone	string	必选	用户名
// password	string	必选	密码
export const reqUserLogin = (data) => {
    return requests({
        url: "/user/passport/login",
        method: "post",
        data, // 简写
    });
};

// token校验获取用户登录信息
// 请求地址 /api/user/passport/auth/getUserInfo  请求方式 get
export const reqUserInfoByToken = () => {
    return requests({
        url: "/user/passport/auth/getUserInfo",
        method: "get",
    });
};

// 退出登陆
// 请求地址 /api/user/passport/logout  请求方式 get  不需要参数
export const reqUserLogout = () => {
    return requests({
        url: "/user/passport/logout",
        method: "get",
    });
};

// 获取用户地址信息
// 请求地址 /api/user/userAddress/auth/findUserAddressList  请求方式 get
export const reqUserAddressList = () => {
    return requests({
        url: "/user/userAddress/auth/findUserAddressList",
        method: "get",
    });
};

// 获取订单交易页信息
// 请求地址 /api/order/auth/trade 请求方式 GET
export const reqOrderTradeInfo = () => {
    return requests({
        url: "/order/auth/trade",
        method: "get",
    });
};

// 提交订单,准备支付
// 请求地址 /api/order/auth/submitOrder?tradeNo={tradeNo} 请求方式 POST 
// 携带参数
// traderNo	string	Y	交易编号(拼接在路径中)
// consignee	string	Y	收件人姓名
// consigneeTel	string	Y	收件人电话
// deliveryAddress	string	Y	收件地址
// paymentWay	string	Y	支付方式
// (ONLINE代表在线)
// orderComment	string	Y	订单备注
// orderDetailList	Array	Y	存储多个商品对象的数组
export const reqSubmitOrder = (tradeNo,data)=>{
    return requests({
        url:`/order/auth/submitOrder?tradeNo=${tradeNo} `,
        method:"post",
        data //简写
    })
}
// 注意：从提交订单开始，就不在使用vuex存储数据了，在组件内直接发请求获取数据


// 获取订单支付信息
// 请求地址 /api/payment/weixin/createNative/{orderId} 请求方式 GET
// 携带参数
// orderId	string	Y	支付订单ID (通过提交订单得到) 
export const reqOrderPayInfo = (orderId)=>{
    return requests({
        url:`/payment/weixin/createNative/${orderId} `,
        method:"get",
    })
}

// 查询支付订单状态
// 请求地址 /api/payment/weixin/queryPayStatus/{orderId} 请求方式 GET
// orderId	string	Y	支付订单ID
export const reqPayStatus = (orderId)=>{
    return requests({
        url:`/payment/weixin/queryPayStatus/${orderId}`,
        method:"get"
    })
}

// 获取我的订单列表
// 请求地址 /api/order/auth/{page}/{limit} 请求方式 GET
// page	string	Y	页码
// limit	string	Y	每页显示数量
export const reqMyOrderList = (page,limit)=>{
    return requests({
        url:`/order/auth/${page}/${limit}`,
        method:"get"
    })
}