<template>
    <!-- 该组件在Home，Search，Detail页面都有使用，所以将该组件注册为全局组件 -->
    <!-- 商品分类导航 -->
    <div class="type-nav">
        <div class="container">
            <div @mouseleave="leaveIndex" @mouseenter="changeShow">
                <h2 class="all">全部商品分类</h2>
                <!-- 全部商品分类具体内容出现时的过渡动画 -->
                <!--  <transition 
                    name="animate__animated animate__bounce"
                    enter-active-class="animate__fadeIn" 
                    leave-active-class="animate__fadeOutBottomLeft"
                > -->
                <transition name="sort">
                    <!-- 三级联动 -->
                    <div class="sort" v-show="Show">
                        <div class="all-sort-list2" @click="goSearch">
                            <!-- 一级（最外层） -->
                            <div
                                class="item bo"
                                v-for="(c1, index) in CategoryList"
                                :key="c1.categoryId"
                                @mouseenter="changeIndex(index)"
                                :class="{ cur: index === currentIndex }"
                            >
                                <h3>
                                    <!-- 三级联动路由跳转
                                    方式一：通过 <router-link ></router-link> 标签可以实现路由跳转
                                    但是这是一个组件，通过循环后，有1000+的router-link组件，这会占用大量的内存-->
                                    <!-- <router-link :to="{
                                        name : 'search',
                                        query:{
                                            categoryName:c1.categoryName,
                                            categoryId:c1.categoryId
                                        }
                                    }">{{ c1.categoryName }}</router-link> -->

                                    <!-- 
                                    方式二：编程式导航，也能实现路由跳转和传参
                                    但是同样的，这样会有1000+的goSearch 回调函数，也会占用大量内存。
                                    注意：去掉a标签的href属性后，鼠标悬浮时不在变成小手-->
                                    <!-- <a @click="goSearch">{{ c1.categoryName }}</a> -->

                                    <!-- 
                                    方式三:编程式导航＋事件委派
                                    这种方式不会占用大量内存，但是存在几个问题：
                                    1.点击盒子空白处也会跳转，而我们需要的是点击a标签中才跳转，怎么确定点击是a标签
                                    2.就算我们点击的是a标签， 我们怎么知道是几级中的a标签
                                    解决问题1:给a标签添加自定义属性 data-categoryName ，拥有该属性的才是a标签
                                    解决问题2：给这三个不同级的 a标签各添加自定义属性 
                                    data-category1Id , data-category2Id , data-category3Id 加以区分-->

                                    <a
                                        :data-categoryName="c1.categoryName"
                                        :data-category1Id="c1.categoryId"
                                        >{{ c1.categoryName }}</a
                                    >
                                </h3>
                                <!-- 二级和三级 -->
                                <div
                                    class="item-list clearfix"
                                    :style="{ display: index == currentIndex ? 'block' : 'none' }"
                                >
                                    <div
                                        class="subitem"
                                        v-for="c2 in c1.categoryChild"
                                        :key="c2.categoryId"
                                    >
                                        <dl class="fore">
                                            <dt>
                                                <a
                                                    :data-categoryName="c2.categoryName"
                                                    :data-category2Id="c2.categoryId"
                                                    >{{ c2.categoryName }}</a
                                                >
                                            </dt>
                                            <dd>
                                                <em
                                                    v-for="c3 in c2.categoryChild"
                                                    :key="c3.categoryId"
                                                >
                                                    <a
                                                        :data-categoryName="c3.categoryName"
                                                        :data-category3Id="c3.categoryId"
                                                        >{{ c3.categoryName }}</a
                                                    >
                                                </em>
                                            </dd>
                                        </dl>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </transition>
            </div>
            <nav class="nav">
                <a href="###">服装城</a>
                <a href="###">美妆馆</a>
                <a href="###">尚品汇超市</a>
                <a href="###">全球购</a>
                <a href="###">闪购</a>
                <a href="###">团购</a>
                <a href="###">有趣</a>
                <a href="###">秒杀</a>
            </nav>
        </div>
    </div>
</template>

<script>
// 引入 lodash (全部引入)
// import _ from "lodash"
// 引入 lodash （ 按需引入：这里引入了throttle（节流函数） 用于三级联动节流 ，防止卡顿）
import throttle from "lodash/throttle";

// 移入animate.css动画库
import "animate.css";

export default {
    name: "TypeNav",
    data() {
        return {
            currentIndex: -1,
            Show: true,
        };
    },
    methods: {
        changeIndex: throttle(function (index) {
            this.currentIndex = index;
        }, 70),
        leaveIndex() {
            this.currentIndex = -1;
            // 在home外的页面，鼠标移出，不在展示全部商品分类具体内容（更改show的值为false
            if (this.$route.path !== "/home") {
                this.Show = false;
            }
        },
        // 三级联动点击a标签，通过编程式导航，实现路由跳转到search页面
        goSearch(event) {
            // console.log(event.target.dataset);
            // event.target.dataset   //例如：categoryname （注意：dataset下没有 data- ）可以获取到自定义的属性和属性值
            let { categoryname, category1id, category2id, category3id } = event.target.dataset; //解构赋值
            // 解决问题1:如果有自定义属性 categoryname，那么一定是a标签
            if (categoryname) {
                // 整理数据
                let location = {
                    name: "search",
                    query:{
                        categoryName: categoryname,
                    }
                };
                // 解决问题2
                if (category1id) {
                    // 确定三级联动中一级a标签
                    location.query.category1Id = category1id;
                } else if (category2id) {
                    // 确定三级联动中二级a标签
                    location.query.category2Id = category2id;
                } else {
                    // 确定三级联动中三级a标签
                    location.query.category3Id = category3id;
                }
                if (this.$route.params.keyword) {
                    location.params = this.$route.params;
                }
                this.$router.push(location);
            }
        },
        // 鼠标移入展示全部商品列表
        changeShow() {
            this.Show = true;
        },
    },

    mounted() {
        /* 当路由跳转到其它其它页面，该组件TypeNav会再次挂载
         这里通过改变 Show 的值，搭配v-show，确定组件内容是否展示 */
        if (this.$route.path !== "/home") {
            this.Show = false;
        }
    },
    computed: {
        CategoryList() {
            return this.$store.state.home.CategoryList;
        },
    },
};
</script>

<style lang="less" scoped>
.type-nav {
    border-bottom: 2px solid #e1251b;

    .container {
        width: 1200px;
        margin: 0 auto;
        display: flex;
        position: relative;

        .all {
            width: 210px;
            height: 45px;
            background-color: #e1251b;
            line-height: 45px;
            text-align: center;
            color: #fff;
            font-size: 14px;
            font-weight: bold;
        }

        .nav {
            a {
                height: 45px;
                margin: 0 22px;
                line-height: 45px;
                font-size: 16px;
                color: #333;
            }
        }

        .sort {
            position: absolute;
            left: 0;
            top: 45px;
            width: 210px;
            height: 461px;
            position: absolute;
            background: #fafafa;
            z-index: 999;

            .all-sort-list2 {
                .item {
                    h3 {
                        line-height: 28px;
                        font-size: 14px;
                        font-weight: 400;
                        overflow: hidden;
                        padding: 0 20px;
                        margin: 0;

                        a {
                            color: #333;
                        }
                    }

                    .item-list {
                        display: none;
                        position: absolute;
                        width: 734px;
                        min-height: 460px;
                        background: #f7f7f7;
                        left: 210px;
                        border: 1px solid #ddd;
                        top: 0;
                        z-index: 9999 !important;

                        .subitem {
                            float: left;
                            width: 650px;
                            padding: 0 4px 0 8px;

                            dl {
                                border-top: 1px solid #eee;
                                padding: 6px 0;
                                overflow: hidden;
                                zoom: 1;

                                &.fore {
                                    border-top: 0;
                                }

                                dt {
                                    float: left;
                                    width: 54px;
                                    line-height: 22px;
                                    text-align: right;
                                    padding: 3px 6px 0 0;
                                    font-weight: 700;
                                }

                                dd {
                                    float: left;
                                    width: 415px;
                                    padding: 3px 0 0;
                                    overflow: hidden;

                                    em {
                                        float: left;
                                        height: 14px;
                                        line-height: 14px;
                                        padding: 0 8px;
                                        margin-top: 5px;
                                        border-left: 1px solid #ccc;
                                    }
                                }
                            }
                        }
                    }
                    //less中 & 代表当前元素 下面等同于 .item:hover {...}
                    // &:hover {
                    //     .item-list {
                    //         display: block;
                    //     }
                    // }
                }
                /* 方式二：当鼠标移动到一级分类上index===currentIndex为true，
                 绑定样式:class="{cur:index===currentIndex}"生效，背景变为天蓝色 */
                .cur {
                    background-color: skyblue;
                }
                /* 方式一：通过修改样式达到，鼠标移动到一级分类上，变为天蓝色
                .item:hover{
                    background-color: skyblue;
                } */
            }
        }
        /*过渡动画:商品分类 进入阶段*/
        .sort-enter {
            height: 0px;
        }
        .sort-enter-active {
            transition: all 0.5s;
        }
        .sort-enter-to {
            height: 461px;
        }
    }
}
</style>
