<template>
    <div class="swiper-container">
        <div class="swiper-wrapper">
            <div
                class="swiper-slide"
                v-for="(skuImage, index) in skuImageList"
                :key="skuImage.id"
                @click="changeCurrentIndex(index)"
            >
                <!-- currentIndex==inde时显示边框样式 -->
                <img :src="skuImage.imgUrl" :class="{ active: currentIndex === index }" />
            </div>
        </div>
        <div class="swiper-button-next"></div>
        <div class="swiper-button-prev"></div>
    </div>
</template>

<script>
import Swiper from "swiper";
export default {
    name: "ImageList",
    data() {
        return {
            // 鼠标点击图片是否显示边框样式的权限初始化为0，即初始化第一张图片有边框样式
            currentIndex: 0,
        };
    },
    props: ["skuImageList"],
    methods: {
        // 用户点击那张图片就将currentIndex变为该图片被遍历时的index，如此currentIndex 等于 当前图片的index 显示边框样式
        changeCurrentIndex(index) {
            this.currentIndex = index;
            // 同时通知兄弟组件Zoom，当前选择图片对应的索引值
            this.$bus.$emit("getIndex",index)
        },
    },
    watch: {
        // 当skuImageList发生变化，执行回调，搭配$nextTick在下次DOM节点更新后调用 new Swiper() 的回调函数
        skuImageList: {
            immediate: true,
            handler() {
                this.$nextTick(() => {
                    var mySwiper = new Swiper(".swiper-container", {
                        // 一次显示多少张图片
                        slidesPerView: 4,
                        // 一次以多少张图片为一组，进行切换
                        slidesPerGroup: 2,
                        // 如果需要前进后退按钮
                        navigation: {
                            nextEl: ".swiper-button-next",
                            prevEl: ".swiper-button-prev",
                        },
                    });
                });
            },
        },
    },
};
</script>

<style lang="less" scoped>
.swiper-container {
    height: 56px;
    width: 412px;
    box-sizing: border-box;
    padding: 0 12px;

    .swiper-slide {
        width: 56px;
        height: 56px;

        img {
            width: 100%;
            height: 100%;
            border: 1px solid #ccc;
            padding: 2px;
            width: 50px;
            height: 50px;
            display: block;

            &.active {
                border: 2px solid #f60;
                padding: 1px;
            }

            // 鼠标移动到图片上方加边框，通过样式实现
            // &:hover {
            //     border: 2px solid #f60;
            //     padding: 1px;
            // }
        }
    }

    .swiper-button-next {
        left: auto;
        right: 0;
    }

    .swiper-button-prev {
        left: 0;
        right: auto;
    }

    .swiper-button-next,
    .swiper-button-prev {
        box-sizing: border-box;
        width: 12px;
        height: 56px;
        background: rgb(235, 235, 235);
        border: 1px solid rgb(204, 204, 204);
        top: 0;
        margin-top: 0;
        &::after {
            font-size: 12px;
        }
    }
}
</style>
