// 对axios进行二次封装
import axios from "axios";

//引入进度条
import nprogress from 'nprogress';
//引入相关进度条的样式
import "nprogress/nprogress.css";

// 在该模块引入Vuex仓库模块即 store
// 主要用于获取保存在Vuex仓库中detail模块中的游客id
import store from "@/store"

// 通过axios提供的create方法，创建一个axios实例
const requests = axios.create({
    // 配置对象
    // 基础路径，在请求的时候，路径中会出现api
    baseURL: "/api",
    // 请求超时的时间为5s
    timeout: 5000,
});
// 请求拦截器：在发请求之前，请求拦截器可以检测到，可以在请求发出去之前做一些事情
requests.interceptors.request.use(function (config) {
    // config是一个配置对象，其中有一个headers请求头属性十分重要
    // 如果有游客id，我们在请求头中加入特殊字段（ userTempId ）和后端商量好的，告知服务器
    if (store.state.detail.uuid_token) {
        config.headers.userTempId = store.state.detail.uuid_token
    }
    // 用户成功登录，获得服务器下发的token(如果有token,在请求头中添加token字段，携带给服务器，获取用户登录信息）
    if (store.state.user.token) {
        config.headers.token = store.state.user.token
    }
    //进度条开始
    nprogress.start()
    return config;
});
// 响应拦截器：在服务器响应数据回来后，响应拦截器会检测到，可以对响应回来的数据做一些事情
requests.interceptors.response.use(
    function (res) {
        //进度条结束
        nprogress.done();

        // 2xx 范围内的状态码都会触发该函数。
        // 对响应数据做点什么
        return res.data;
    },
    function (error) {
        // 超出 2xx 范围的状态码都会触发该函数。
        // 对响应错误做点什么
        return Promise.reject(error);
    }
);

//最后需要暴露:暴露的是添加新的功能的axios,即为requests
export default requests;
