                                        >综合<span
                                            v-show="isOne"
                                            class="iconfont"
                                            :class="{ 'icon-up': isAsc, 'icon-down': isDesc }"
                                        ></span
                                    ></a>
                                </li>
                                <li :class="{ active: isTwo }" @click="changeOrder('2')">
                                    <a
                                        >价格<span
                                            v-show="isTwo"
                                            class="iconfont"
                                            :class="{ 'icon-up': isAsc, 'icon-down': isDesc }"
                                        ></span
