                    </div>
                    <!-- goods-list 商品展示列表 -->
                    <div class="goods-list">
                        <ul class="yui3-g">
                            <li class="yui3-u-1-5" v-for="goods in goodsList" :key="goods.id">
                                <div class="list-wrap">
                                    <div class="p-img">
                                        <!-- 点击search页码商品图片，通过声明式导航进行路由跳转，跳转到详情页面detail -->
                                        <router-link
                                            :to="{
                                                name: 'detail',
                                                params: {
                                                    skuId: goods.id,
                                                },
                                            }"
                                        >
                                            <!-- v-lazy 图片懒加载（vue-lazyload插件），在服务器数据回来前，图片显示预设的本地图片 -->
                                            <!-- <img :src="goods.defaultImg" > -->
                                            <img v-lazy="goods.defaultImg" />
                                        </router-link>
                                    </div>
                                    <div class="price">
                                        <strong>
                                            <em>¥</em>
                                            <i>{{ goods.price }}</i>
                                        </strong>
                                    </div>
                                    <div class="attr">
                                        <a
                                            target="_blank"
                                            href="item.html"
                                            title="促销信息，下单即赠送三个月CIBN视频会员卡！【小米电视新品4A 58 火爆预约中】"
                                            >{{ goods.title }}</a
                                        >
                                    </div>
