                                        <img src="./images/like_02.png" />
                                    </div>
                                    <div class="attr">
                                        <em>Apple苹果iPhone 6s (A1699)</em>
                                    </div>
                                    <div class="price">
                                        <strong>
                                            <em>¥</em>
                                            <i>4088.00</i>
                                        </strong>
                                    </div>
                                    <div class="commit">
                                        <i class="command">已有700人评价</i>
                                    </div>
                                </div>
                            </li>
                        </ul>
                    </div>
                </div> -->
            </div>
        </div>
    </div>
</template>

<script>
import { mapGetters } from "vuex";

// 引入子组件SearchSelector
import SearchSelector from "./SearchSelector";

export default {
    name: "Search",
    // 路由组件可以传递props参数
    // props: ["keyword"],
    data() {
        return {
            // 发请求时，要携带的参数
            searchParams: {
                category1Id: "", //一级分类的id
                category2Id: "", //二级分类的id
                category3Id: "", //三级分类的id
                categoryName: "", //商品的名字
                keyword: "", //用户搜索的关键字
                props: [], //商品属性的搜索条件
                trademark: "", //品牌的搜索条件

                /* 排序方式 
                1: 综合,2: 价格 asc: 升序,desc: 降序  
                示例: "1:desc" 
                 怎么判断 ：
                indexOf判断order是否有1或者2  为1代表综合有样式，为2代表价格有样式
                 indexOf判断order是否有asc或者desc 为asc展示上箭头有样式，为2展示下箭头有样式 */
                order: "1:desc", //排序的参数 【默认初始值:1:desc】

                pageNo: 1, //当前分页器的页码  【默认初始值:1】
                pageSize: 2, //代表当前一页显示几条数据 【默认初始值:10】 控制服务器返回的goodsList数据有多少条(每页渲染多少条数据)
            },
        };
    },

    components: {
        // 注册子组件
        SearchSelector,
    },
    computed: {
        ...mapGetters("search", ["goodsList", "totalData"]),

        // searchParams中order有1的话indexOf不会返回-1 ，isOne为true，综合有样式
        isOne() {
            return this.searchParams.order.indexOf(1) !== -1;
        },
        // searchParams中order有2的话indexOf不会返回-1 ，isTwo为true，价格有样式
        isTwo() {
            return this.searchParams.order.indexOf(2) !== -1;
        },
        // searchParams中order有asc的话indexOf不会返回-1 ，isAsc为true，上箭头升序
        isAsc() {
            return this.searchParams.order.indexOf("asc") !== -1;
        },
        // searchParams中order有desc的话indexOf不会返回-1 ，isDesc为true，下箭头降序
        isDesc() {
            return this.searchParams.order.indexOf("desc") !== -1;
        },
    },
    methods: {
        // 封装一个获取search页面数据的函数
        getData() {
            this.$store.dispatch("search/getSearchList", this.searchParams);
        },
        // 点击面包蟹的 x 移除 categoryName
        removeCategoryName() {
            // 这里用undefined不用空字符串"",是为了优化发请求时要携带的参数数据，参数值为undefined时，在向后台请求数据时，可以不携带
            this.searchParams.categoryName = undefined;
            // 既然要重新再次发请求，而且点击x删除了categoryName，那么为了不影响本次请求，同时要将以前的categoryId数据清空，这里同样可以用undefined
            this.searchParams.category1Id = undefined;
            this.searchParams.category2Id = undefined;
            this.searchParams.category3Id = undefined;
            // 发请求获取新数据
            this.getData();
            // 在移除 categoryName 后，我们网址信息也应该发生变化，这里采用路由跳转的方法
            this.$router.push({
                name: "search",
                //如果有params参数表示，Header模块中的搜索框有数据，也需要携带过去
                // 因为这里的实质其实是删除query参数（三级联动的参数）
                params: this.$route.params,
            });
        },
        // 点击面包蟹的 x 移除 keyword
        removeKeyword() {
            this.searchParams.keyword = undefined;
            // 重新请求数据
            this.getData();
            // 在我们删除关键字后，我们需要让Header组件中的搜索框清空，这里涉及到了组件件通信，这里采用全局事件总线解决
            this.$bus.$emit("clearKeyword");
            // 在移除 keyword 后，我们网址信息也应该发生变化，这里采用路由跳转的方法
            this.$router.push({
                name: "search",
                query: this.$route.query,
            });
        },
        //自定义事件trademarkInfo（点击商标触发）被触发后 的回调函数
        trademarkInfo(trademark) {
            //通过形参获取到子组件触发该自定义事件传递的，有关商标的数据
            //  通过接口文档可知要携带的参数 searchParams中的trademark字段，格式为 "ID:品牌名称"
            this.searchParams.trademark = `${trademark.tmId}:${trademark.tmName}`;
            // 重新发起请求获取服务器数据
            this.getData();
        },
        // 点击面包蟹的 x 移除 trademark
        removeTrademark() {
            this.searchParams.trademark = undefined;
            // 再次发请求
            this.getData();
        },
        // 自定义事件attrsInfo （点击商品属性值触发） 被触发后的回调函数  根据接口文档props格式：["属性ID:属性值:属性名"]
        attrsInfo(attrs, attrValue) {
            // 声明一个字符串props 用来接受参数
            let propsStr = `${attrs.attrId}:${attrValue}:${attrs.attrName}`;
            // 数组去重
            // 判读 searchParams 对象中的 props数组是否已经有相同的元素，没有的话indexOf会返回-1,并将其放入props数组中
            if (this.searchParams.props.indexOf(propsStr) == -1) {
                this.searchParams.props.push(propsStr);
            }
            // 重新请求数据
            this.getData();
        },
        // 点击面包蟹的 x 移除 attrValue
        removeAttrValue(index) {
            // 再次整理参数，将移除的attrValue，从props数组中删除
            this.searchParams.props.splice(index, 1);
            // 然后重新请求数据
            this.getData();
        },
        // 点击排序标签
        changeOrder(flag) {
            // flag是形参用于标记用户点击的是 综合排序还是价格排序
            // 首先确定初始的order是怎么样的 包括 标记和排序方式
            let originOrderFlag = this.searchParams.order.split(":")[0];
            let originOrderSort = this.searchParams.order.split(":")[1];
            // 准备一个新的order字符串
            let newOrderStr = "";
            // 比较形参标记和源标记 是否一致，一致表示，用户点击的和原本的是一致的，本项目初始为标记为1即综合
            if (flag == originOrderFlag) {
                // 这表示最开始用户点击的是综合
                newOrderStr = `${flag}:${originOrderSort == "desc" ? "asc" : "desc"}`;
                console.log(newOrderStr);
            } else {
                newOrderStr = `${flag}:${"desc"}`;
                console.log(newOrderStr);
            }
            this.searchParams.order = newOrderStr;
            this.getData();
        },
        // 自定义事件 getPageNo 的回调函数，当用户点击分页器按钮，通过该回调函数获取到传递过来的希望跳转的页码pageNo
        getPageNo(pageNo) {
            // 将searchParams中的pageNo改为希望跳转的页码，重新发请求，重新渲染页面
            // 整理数据
            this.searchParams.pageNo = pageNo;
            this.getData();
        },
    },
    beforeMount() {
        // 在页面挂载前，将从home页面和搜索框获取到的query参数和params参数，与searchParams进行合并
        Object.assign(this.searchParams, this.$route.query, this.$route.params);
    },
    mounted() {
        // 页面挂载后向后台服务器请求search页面的数据
        this.getData();
        // mounted 只会执行一次，所以这里只会在页面挂载时发送一次Ajax请求
    },
    watch: {
        // 注意，watch配置项可以监听组件实例上的属性的属性值变化，而$route和data中的数据是平级关系，也可以被监视（computed计算属性也可以）
        $route() {
            // 每当用户点击三级联动或者Head组件中的搜索按钮时，都会进行路由跳转，最明显的就是$route中的路径会发生变化
            // 所以我们可以在这个时候，携带新的searchParams参数，重新发Ajax请求，获取新的数据
            // 在这之前，由于路由跳转携带了不同的参数，所以重新整理，要携带的searchParams
            Object.assign(this.searchParams, this.$route.query, this.$route.params);
            this.getData();
            // 清空本次携带的category1Id，category2Id，category3Id ，以防止下一次请求将本次产生的数据再次传回服务器
            // 而 由于每次请求，都会重新覆盖之前的关键字和categoryName，所以这两项不用清空
            this.searchParams.category1Id = "";
            this.searchParams.category2Id = "";
            this.searchParams.category3Id = "";
        },
    },
};
</script>

