// 路由配置项信息模块

// 引入路由组件
import Home from "@/pages/Home";
import Search from "@/pages/Search";
import Login from "@/pages/Login";
import Register from "@/pages/Register";
import Detail from "@/pages/Detail";
import AddCartSuccess from "@/pages/AddCartSuccess";
import ShopCart from "@/pages/ShopCart";
import Trade from "@/pages/Trade";
import Pay from "@/pages/Pay";
import PaySuccess from "@/pages/PaySuccess";
import Center from "@/pages/Center";
import MyOrder from "@/pages/Center/MyOrder";
/* 
路由懒加载（优点：高效）：
当打包构建应用时，JavaScript 包会变得非常大，影响页面加载。
如果我们能把不同路由对应的组件分割成不同的代码块，然后当路由被访问的时候才加载对应组件，这样就会更加高效。
当路由被访问的时候才加载对应组件（当路由被访问时才执行 GroupOrder 函数）
将
import GroupOrder from "@/pages/Center/GroupOrder";
变为
const GroupOrder = () => import("@/pages/Center/GroupOrder"); // 箭头函数的话简写可以省略return
使用：
const router = createRouter({
  // ...
  routes: [{ path: 'xxxx', component: GroupOrder }],
})
 */

export default [
    {
        name: "home",
        path: "/home",
        component: Home,
        // 添加元信息，进行权限判断
        meta: { showFooter: true },
    },
    {
        name: "detail",
        path: "/detail/:skuId?", //skuId为每个商品对应的id
        component: Detail,
        // 添加元信息，进行权限判断,showFooter表示是否显示
        meta: { showFooter: true },
    },
    {
        name: "search",
        // 如果要收到params参数，要进行占位，而且在跳转路由的时候只能通过name的方式，不能使用path的方式
        // 指定params参数可传可不传：在占位的后面加一个?
        path: "/search/:keyword?",
        component: Search,
        meta: { showFooter: true },
        // 路由组件可以传递props参数，且props有三种写法：
        // 对象写法：可以接收到a,b的值
        // props:{a:"1",b:2}

        // 布尔值写法：只可以传递params参数
        // props:true

        // 函数形式，可以获取到$route这个实参
        // props($route) {
        //     return {
        //         keyword: $route.params.keyword,
        //     };
        // },
    },
    {
        // 加入购物车显示成功页面
        name: "addcartsuccess",
        path: "/addcartsuccess",
        component: AddCartSuccess,
        meta: { showFooter: true },
    },
    {
        // 购物车页面
        name: "shopcart",
        path: "/shopcart",
        component: ShopCart,
        meta: { showFooter: true },
    },
    {
        // 交易订单页面
        name: "trade",
        path: "/trade",
        component: Trade,
        meta: { showFooter: true },
        // 路由独享守卫
        beforeEnter: (to, from, next) => {
            // 在进入该路由前，必须通过 购物车页面点击结算按钮进入
            if (from.path == "/shopcart") {
                // 只能从购物车页面来才放行
                next();
            } else {
                next(false); //当url发生改变，放行到来的路由页面
            }
        },
    },
    {
        // 支付页面
        name: "pay",
        path: "/pay",
        component: Pay,
        meta: { showFooter: true },
        // 路由独享守卫
        beforeEnter: (to, from, next) => {
            // 在进入该路由前，必须通过 交易页面点击提交订单按钮进入
            if (from.path == "/trade") {
                // 只能从交易页面来才放行
                next();
            } else {
                next(false); //当url发生改变，放行到来的路由页面
            }
        },
    },
    {
        // 支付订单成功页面
        name: "paySuccess",
        path: "/paySuccess",
        component: PaySuccess,
        meta: { showFooter: true },
        // 使用路由独享守卫的方法 防止从其它页面进入支付成功页面
        //  路由独享守卫
        // beforeEnter: (to, from, next) => {
        //     // 在进入该路由前，必须通过 支付页面支付成功后才能进入
        //     if (from.path == "/pay") {
        //         // 只能从支付页面来才放行
        //         next();
        //     } else {
        //         next(false); //当url发生改变，放行到来的路由页面
        //     }
        // },
        //为了学习，我们放弃这种方法，采用组件内路由守卫的方式防止用户通过其它页面进入支付成功页面
    },
    {
        // 付款成功后的订单管理页面（订单中心）
        name: "center",
        path: "/center",
        component: Center,
        meta: { showFooter: true },
        children: [
            {
                // 订单中心子组件（我的订单）
                path: "myorder",
                name: "my_order",
                component: MyOrder,
            },
            {
                // 订单中心子组件（团购订单）
                path: "grouporder",
                name: "group_order",
                // component: GroupOrder, // 正常引入后，注册组件
                // component: GroupOrder, // 路由懒加载 ，用GroupOrder接受
                component: () => import("@/pages/Center/GroupOrder"), // 路由懒加载 ，直接简写
            },
            {
                // 用戶一访问订单中心就显示我的订单
                path: "/center",
                redirect: "/center/myorder",
            },
        ],
    },
    {
        name: "login",
        path: "/login",
        component: Login,
        meta: { showFooter: false },
    },
    {
        name: "register",
        path: "/register",
        component: Register,
        meta: { showFooter: false },
    },
    {
        // 重定向，在项目跑起来的时候，访问/ 立马让他定向到home页
        path: "*",
        redirect: "/home",
    },
];
