<template>
    <div class="register-container">
        <!-- 注册内容 -->
        <div class="register">
            <h3>
                注册新用户
                <span class="go">我有账号，去 <a href="login.html" target="_blank">登陆</a> </span>
            </h3>
            <!-- 手机号 -->
            <div class="content">
                <label>手机号:</label>
                <input
                    type="text"
                    placeholder="请输入你的手机号"
                    v-model="phone"
                    name="phone"
                    v-validate="{ required: true, regex: /^1\d{10}$/ }"
                    :class="{ invalid: errors.has('phone') }"
                />
                <!-- <input type="text" placeholder="请输入你的手机号" v-model="phone" /> -->
                <span class="error-msg">{{ errors.first("phone") }}</span>
            </div>
            <!-- 验证码 -->
            <div class="content">
                <label>验证码:</label>
                <input
                    type="text"
                    placeholder="请输入验证码"
                    v-model="code"
                    name="code"
                    v-validate="{ required: true, regex: /^\d{6}$/ }"
                    :class="{ invalid: errors.has('code') }"
                />
                <!-- <input type="text" placeholder="请输入验证码" v-model="code" /> -->
                <button style="width: 100px; height: 38px; margin-left: 20px" @click="getCode">
                    获取验证码
                </button>

                <span class="error-msg">{{ errors.first("code") }}</span>
            </div>
            <!-- 密码 -->
            <div class="content">
                <label>登录密码:</label>
                <input
                    type="password"
                    placeholder="请输入你的登录密码"
                    v-model="password"
                    name="password"
                    v-validate="{ required: true, regex: /^[0-9a-zA-Z]{6,15}$/ }"
                    :class="{ invalid: errors.has('password') }"
                />
                <!-- <input type="password" placeholder="请输入你的登录密码" v-model="password" /> -->
                <span class="error-msg">{{ errors.first("password") }}</span>
            </div>
            <!-- 确认密码 -->
            <div class="content">
                <label>确认密码:</label>
                <input
                    type="password"
                    placeholder="请确认你的登录密码"
                    v-model="password1"
                    name="password1"
                    v-validate="{ required: true, is: password }"
                    :class="{ invalid: errors.has('password1') }"
                />
                <!-- <input type="password" placeholder="请输入确认密码" v-model="password1" /> -->
                <span class="error-msg">{{ errors.first("password1") }}</span>
            </div>
            <!-- 协议勾选 -->
            <div class="controls">
                <input
                    type="checkbox"
                    :checked="isAgree"
                    name="agree"
                    v-validate="{ required: true, tongyi: isAgree }"
                    :class="{ invalid: errors.has('agree') }"
                />
                <!-- tongyi是我们要验证的是否为真的项目 而 name="agree"中 agree是我们给该项验证取的名字，我们设置它转换为中文叫协议 -->
                <!-- <input name="m1" type="checkbox" :checked="isAgree" /> -->
                <span>同意协议并注册《尚品汇用户协议》</span>
                <!-- 显示name 为 agree（协议）的错误信息 -->
                <span class="error-msg">{{ errors.first("agree") }}</span>
            </div>
            <div class="btn">
                <button @click="regUser">完成注册</button>
            </div>
        </div>

        <!-- 底部 -->
        <div class="copyright">
            <ul>
                <li>关于我们</li>
                <li>联系我们</li>
                <li>联系客服</li>
                <li>商家入驻</li>
                <li>营销中心</li>
                <li>手机尚品汇</li>
                <li>销售联盟</li>
                <li>尚品汇社区</li>
            </ul>
            <div class="address">地址：北京市昌平区宏福科技园综合楼6层</div>
            <div class="beian">京ICP备19006430号</div>
        </div>
    </div>
</template>

<script>
export default {
    name: "Register",
    data() {
        return {
            // 用户输入电话号码
            phone: "",
            // 验证码
            code: "",
            // 密码
            password: "",
            // 验证密码
            password1: "",
            // 同意协议初始化为真
            isAgree: true,
        };
    },
    methods: {
        // 获取验证码
        getCode() {
            let { phone } = this;
            // 简单判定phone不为空才派发action
            phone &&
                this.$store
                    .dispatch("user/getCode", phone)
                    .then(() => {
                        // 成功获取到验证码，直接将他在注册页面展示出来（这是我们模拟的，现实是用户自己输入的验证码）
                        this.code = this.$store.state.user.code;
                    })
                    .catch((error) => {
                        alert(error.message);
                    });
        },
        // 完成注册用户
        async regUser() {
            let { phone, password, code, isAgree, password1 } = this;
            const success = await this.$validator.validateAll(); //全部表单验证，在验证页面定义，不是在验证模块定义
            // console.log(success); //表单验证都通过为true ，有一个不通过为false
            // 当phone 和 code都不为空, password等于password1 ,isAgree同意协议，再派发action
            if (success) {
                phone &&  password == password1 && isAgree &&
                    this.$store
                        .dispatch("user/regUser", { phone, password, code })
                        .then(() => {
                            // 注册成功，进行路由跳转到登录页面
                            this.$router.push({
                                name: "login",
                            });
                        })
                        .catch((error) => {
                            alert(error.message);
                        });
            }
        },
    },
};
</script>

<style lang="less" scoped>
.register-container {
    .register {
        width: 1200px;
        height: 445px;
        border: 1px solid rgb(223, 223, 223);
        margin: 0 auto;

        h3 {
            background: #ececec;
            margin: 0;
            padding: 6px 15px;
            color: #333;
            border-bottom: 1px solid #dfdfdf;
            font-size: 20.04px;
            line-height: 30.06px;

            span {
                font-size: 14px;
                float: right;

                a {
                    color: #e1251b;
                }
            }
        }

        div:nth-of-type(1) {
            margin-top: 40px;
        }

        .content {
            padding-left: 390px;
            margin-bottom: 18px;
            position: relative;

            label {
                font-size: 14px;
                width: 96px;
                text-align: right;
                display: inline-block;
            }

            input {
                width: 270px;
                height: 38px;
                padding-left: 8px;
                box-sizing: border-box;
                margin-left: 5px;
                outline: none;
                border: 1px solid #999;
            }

            img {
                vertical-align: sub;
            }

            .error-msg {
                position: absolute;
                top: 100%;
                left: 495px;
                color: red;
            }
        }

        .controls {
            text-align: center;
            position: relative;

            input {
                vertical-align: middle;
            }

            .error-msg {
                position: absolute;
                top: 100%;
                left: 495px;
                color: red;
            }
        }

        .btn {
            text-align: center;
            line-height: 36px;
            margin: 17px 0 0 55px;

            button {
                outline: none;
                width: 270px;
                height: 36px;
                background: #e1251b;
                color: #fff !important;
                display: inline-block;
                font-size: 16px;
            }
        }
    }

    .copyright {
        width: 1200px;
        margin: 0 auto;
        text-align: center;
        line-height: 24px;

        ul {
            li {
                display: inline-block;
                border-right: 1px solid #e4e4e4;
                padding: 0 20px;
                margin: 15px 0;
            }
        }
    }
}
</style>
